import Utilities from '../common/utilities';
import './user.service';
import constants from '../common/constants';


export default function protectedResourceInterceptor($q, $log, $injector) {
    var userService = $injector.get('userService');

    return {
        request: function (config) {
            var deferred = $q.defer();

            if (Utilities.isUndefinedOrNull(config) || Utilities.isUndefinedOrNull(config.headers)) {
                $log.error('protectedResourceInterceptor.request: http request config is null or undefined');
                deferred.resolve(config);
                return deferred.promise;
            }

            var userType = config.headers['X-UserType'];

            // Only apply http config changes to requests that have custom header
            if (Utilities.isUndefinedOrNull(userType)) {
                deferred.resolve(config);
                return deferred.promise;
            }

            var resource = config.headers['X-Resource'];

            // Remove the custom header; no longer needed
            delete config.headers['X-Resource'];
            delete config.headers['X-UserType'];
            userService.acquireToken(userType, config.url, resource).then(token => {
                // Validate userType within acquireToken method
                if (!Utilities.isUndefinedOrNull(token)) {
                    config.headers.Authorization = 'Bearer ' + token;
                }
                else {
                    $log.warn(`failed to get auth token for type:${userType} url: ${config.url}, resource ${resource}`);
                }
                deferred.resolve(config);
            });

            return deferred.promise;
        },
        responseError: function (response) {
            if (response && response.status === 401) {
                if (Utilities.isNotUndefinedOrNull(response.config) &&
                    (response.config.url === constants.O365CONFIG.SHAREPOINT_URL || response.config.url === constants.O365CONFIG.PHOTO_URL)) {
                    // Don't clear the token if the request is made to get the sharepoint site url or the user photo.
                    return $q.reject(response);
                }

                userService.clearToken();
            }

            return $q.reject(response);
        }
    };
}
