import angular from 'angular';
import protectedResourceInterceptor from './protectedResourceInterceptor';
import userService from './user.service';
import msidUserService from './msidUser.service';
import msaUserService from './msaUser.service';
import o365UserService from './o365User.service';
import '../common';

export default angular
    .module('app.user', [])
    .config(['$httpProvider', '$provide', appUser])
    .factory('protectedResourceInterceptor', ['$q', '$log', '$injector', protectedResourceInterceptor])
    .service('msaUserService', ['$log', '$injector', msaUserService])
    .service('o365UserService', ['$log', '$injector', 'notification', o365UserService])
    .service('msidUserService', ['$log', '$injector', 'notification', msidUserService])
    .factory('userService', ['$q', '$log', '$injector', 'storage', 'notification', 'msaUserService', 'o365UserService', 'msidUserService', userService])
    .name;

function appUser($httpProvider, $provide) {
    // Register http interceptors
    $httpProvider.interceptors.push('protectedResourceInterceptor');
}
