import Utilities from '../common/utilities';
import BrowserHandler from '../common/browserHandler';
import angular from 'angular';
import constants from '../common/constants';
import {AWTLogManager} from '@aria/webjs-sdk';

export default function telemetry($injector, ariaTelemetry) {
    var telemetryProviders = [ariaTelemetry];

    var addCustomProperties = async function (properties) {
        var userService = $injector.get('userService');

        // Append app specific properties
        if (Utilities.isUndefinedOrNull(properties)) {
            properties = {};
        }

        properties.pii = [];
        var manifest = Utilities.getManifest();
        if (manifest) {
            properties.appVersion = manifest.version;
        }

        // Get UI language
        var language = BrowserHandler.i18n.getUILanguage();
        if (Utilities.isNotUndefinedOrNull(language)) {
            properties.language = language;
        }

        // Add AppInfo.Name for GDPR T3 export
        properties['AppInfo.Name'] = constants.APPINFO_NAME;
        // Add user info properties
        var userType = await userService.getUserType();
        var semanticContext = AWTLogManager.getSemanticContext();

        if (userType === constants.USER_TYPE.NONE) {
            // Clear UserInfo.Id and UserInfo.IdType context
            semanticContext.setUserId('', '', '');
            return properties;
        }

        var userInfo = await userService.getUserInfo(userType);
        if (Utilities.isUndefinedOrNull(userInfo)) {
            // Clear UserInfo.Id and UserInfo.IdType context
            semanticContext.setUserId('', '', '');
            return properties;
        }

        // userInfo properties - cid from MSA
        if (Utilities.isNotUndefinedOrNull(userInfo.cid)) {
            properties.cid = userInfo.cid;
            // Instrument AWT about UserInfo.Id and UserInfo.IdType for GDPR requirements
            semanticContext.setUserId(userInfo.cid);
            properties['UserInfo.IdType'] = constants.IDTYPE.MSACID;
        }

        // userInfo properties - features (from ExP treatment assignment)
        if (Utilities.isNotUndefinedOrNull(userInfo.features)) {
            properties.experimentFeatures = userInfo.features;
        }

        // PII identity properties - puid from O365
        if (Utilities.isNotUndefinedOrNull(userInfo.puid)) {
            properties.pii.push({type: constants.TELEMETRY.PII_TYPE.IDENTITY, name: 'puid', value: userInfo.puid});
            // Instrument AWT about UserInfo.Id and UserInfo.IdType for GDPR requirements
            semanticContext.setUserId(userInfo.puid);
            properties['UserInfo.IdType'] = constants.IDTYPE.ORGIDPUID;
        }

        if (Utilities.isNotUndefinedOrNull(userInfo.tid)) {
            properties['UserInfo.OMSTenantId'] = userInfo.tid;
        }

        return properties;
    };

    // Parses correctly the value of setting
    // Returns undefined if the value cannot be parsed.
    //
    // NOTE: Cannot import this from editorUtils as it would
    // create a DI loop.
    var readSettingValueOrUndefined = function (settings, settingName) {
        if (!settings[settingName]) {return undefined}

        var storedSetting = JSON.parse(settings[settingName]);

        if (storedSetting.value === undefined) {return undefined}

        return JSON.parse(storedSetting.value);
    };

    var getEnabledSettingFromStorage = function () {
        var $q = $injector.get('$q');
        var storage = $injector.get('storage');
        var deferred = $q.defer();

        storage.get(['telemetry_enabled', 'enableTelemetry']).then(function (result) {
            deferred.resolve(
                Utilities.isUndefinedOrNull(result)
                    || readSettingValueOrUndefined(result, 'enableTelemetry') === true
                    || Utilities.isUndefinedOrNull(result.telemetry_enabled)
                    || result.telemetry_enabled === true
                    ? true : false);
        });

        return deferred.promise;
    };

    var setEnabledSettingInStorage = function (enabled) {
        var storage = $injector.get('storage');
        storage.set({
            'telemetry_enabled': enabled,
            'enableTelemetry': JSON.stringify(
                {
                    value: JSON.stringify(enabled),
                    lastUpdate: Date.now(),
                    manualOverride: false
                }
            ),
        });
    };

    var service = {
        // Get telemetry setting from storage
        getEnabledSetting: function () {
            return getEnabledSettingFromStorage();
        },

        // Set telemetry setting from storage
        setEnabledSetting: function (enabled) {
            // App Insights workaround: send message to background instance to handle sending telemetry data
            if (!Utilities.isBackgroundContext()) {
                BrowserHandler.runtime.sendMessage({
                    activity: constants.ACTIVITY.TELEMETRY.NAME,
                    command: constants.TELEMETRY.COMMAND.SET_DISABLED,
                    enabled: enabled
                });

                return;
            }

            // Cache setting in local storage
            setEnabledSettingInStorage(enabled);
            for (var i = 0; i < telemetryProviders.length; ++i) {
                telemetryProviders[i].setEnabledSetting(enabled);
            }
        },

        // Use to track custom application events and sends to application insights
        trackEvent: async function (eventName, properties, measurements) {
            if (angular.isUndefined(eventName) || !angular.isString(eventName)) {
                return;
            }

            // Send message to background instance to handle sending telemetry data
            if (!Utilities.isBackgroundContext()) {
                BrowserHandler.runtime.sendMessage({
                    activity: constants.ACTIVITY.TELEMETRY.NAME,
                    command: constants.TELEMETRY.COMMAND.TRACK_EVENT,
                    eventName: eventName,
                    properties: properties,
                    measurements: measurements
                });

                return;
            }

            properties = await addCustomProperties(properties);
            for (var i = 0; i < telemetryProviders.length; ++i) {
                var propertiesToTrack = angular.extend({}, properties);
                telemetryProviders[i].trackEvent(eventName, propertiesToTrack, measurements);
            }

        },

        // Use to track trace messages (ie. logging/diagnostics info) and sends to application insights
        trackTrace: async function (message, properties) {
            if (angular.isUndefined(message) || !angular.isString(message)) {
                return;
            }

            // Send message to background instance to handle sending telemetry data
            if (!Utilities.isBackgroundContext()) {
                BrowserHandler.runtime.sendMessage({
                    activity: constants.ACTIVITY.TELEMETRY.NAME,
                    command: constants.TELEMETRY.COMMAND.TRACK_TRACE,
                    message: message,
                    properties: properties
                });

                return;
            }

            properties = await addCustomProperties(properties);
            for (var i = 0; i < telemetryProviders.length; ++i) {
                var propertiesToTrack = angular.extend({}, properties);
                telemetryProviders[i].trackTrace(message, propertiesToTrack);
            }
        }
    };

    // return the local instance when called
    return service;
}
