import constants from '../common/constants';

export default function msidUserService($log, $injector, notification) {
    const type = constants.USER_SERVICE_TYPE.MSID;
    var userService = undefined;
    // previous app registration also included (are they still needed?)
    // Graph:
    // offline_access, Directory.AccessAsUser.All, User.Read.All
    // SharePoint:
    // MyFiles.Read, MyFiles.Write
    const scopes = 'email openid profile user.read';
    const userInfoUrl = 'https://graph.microsoft.com/v1.0/me/';
    const aadV2Url = new URL('https://login.microsoftonline.com/common/oauth2/').toString();
    const msaOAuthUrl = new URL('https://login.live.com/oauth20_authorize.srf').toString();
    const redirectUri = new URL('nativeclient', aadV2Url).toString();
    const logoutUrl = new URL('v2.0/logoutsession', aadV2Url).toString();
    const authorizeUrl = new URL('v2.0/authorize', aadV2Url).toString();
    const aadClientId = 'e28ff72c-58a5-49ba-8125-42ec264d7cd0'; // newest clientId
    const loginUrl = (() => {
        var loginUrl = new URL(authorizeUrl);
        loginUrl.search = createParams({prompt: 'select_account'});
        return loginUrl.toString();
    })();

    function getUserService() {
        if (!userService) {
            userService = $injector.get('userService');
        }

        return userService;
    }

    function createParams(additionalParams) {
        const defaultParams = {
            'client_id': aadClientId,
            'response_type': 'id_token token',
            'redirect_uri': redirectUri,
            'scope': scopes,
            'response_mode': 'fragment',
        };
        const params = new URLSearchParams(Object.assign({}, defaultParams, additionalParams));
        return params.toString();
    }

    // Could we use chrome.identity.launchWebAuthFlow()
    // with redirect url https://ndjpnladcallmjemlbaebfadecfhkepb.chromiumapp.org/response?
    // see https://developer.chrome.com/extensions/identity#method-launchWebAuthFlow
    function getConfig() {
        return {
            'loginUrl': loginUrl,
            'logoutUrl': logoutUrl,
            'photoUrl': 'https://graph.microsoft.com/beta/me/photo/$value',
            'userInfoUrl': userInfoUrl,
            'documentsUrl': 'https://graph.microsoft.com/v1.0/me/drive/recent'
        };
    }

    async function handleLogin() {
        const msaParams = {
            'url': msaOAuthUrl,
            'aadredir': 1,
            'prompt': 'select_account'
        };

        const aadParams = {
            'url': authorizeUrl,
            'prompt': 'select_account'
        };

        // TODO: Not sure if aadv2 requires dual stack approach where we try one endpoint and then another but if
        // so we then need to determine if we should start with AAD or MSA but default to MSA since it can
        // handle sign up in case they want to create a new account for overview on how to handle dual stack
        // select_account see https://identitydocs.azurewebsites.net/static/overview/user_discovery.html
        const authEndpointParams = [];
        // authEndpointParams.push(msaParams); // not adding until app configuration is updated to support msa
        authEndpointParams.push(aadParams);

        var accountId = undefined;
        for (var endpointParams of authEndpointParams) {
            var loginUrl = new URL(endpointParams.url);
            delete endpointParams.url;

            if (accountId) {
                endpointParams.set('login_hint', accountId);
            }
            loginUrl.search = createParams(endpointParams);
            var response = await getUserService().navigateToAuthEndpoint(loginUrl.toString(), type);
            if (!response.error || response.error === 'aad_auth') {
                return;
            }

            if (response.username) {
                accountId = response.username;
            }
        }
    }

    async function lookupUserInfo(token) {
        const infoFuncs = [
            fetchUserInfo(),
            fetchOneDriveUrl(),
            fetchOneDriveQuota()
        ];
        var results = await Promise.all(infoFuncs);

        return results.reduce((previous, current) => {
            return Object.assign(previous, current);
        });
    }

    function deleteMetaDataTags(object) {
        for (const prop in object) {
            if (object.hasOwnProperty(prop)) {
                if (prop.match(/^@odata/i)) {
                    delete object[prop];
                }
            }
        }
        return object;
    }

    async function fetchUserInfo() {
        var request = {
            method: 'GET',
            url: userInfoUrl,
            headers: {
                'X-UserType': type
            }
        };

        var userInfo = {};
        try {
            var response = await getUserService().httpRequest(request);
            userInfo = deleteMetaDataTags(response);
        }
        catch (reason) {
            return Promise.reject(`msidUser.fetchUserInfo failed: ${reason}`);
        }

        return userInfo;
    }

    async function fetchOneDriveUrl() {
        var request = {
            method: 'GET',
            url: constants.O365CONFIG.ONEDRIVE_URL,
            headers: {
                'X-UserType': type
            }
        };

        try {
            var data = await getUserService().httpRequest(request);
            if (data.webUrl) {
                return {'oneDriveUrl': data.webUrl};
            }

            // There is no service info for OD4B
            return Promise.reject('o365UserService.DiscoverServiceEndpoint - Invalid resource');
        }
        catch (reason) {
            notification.show(constants.NOTIFICATION.INVALIDSUBSCRIPTION);
            $log.error(`msidUser.fetchOneDriveUrl failed: ${JSON.stringify(reason)}`);
            return null;
        }
    }

    // accounts.microsoft.com makes a call to
    // https://account.microsoft.com/services/api/subscriptions?X-Requested-With=XMLHttpRequest&excludeWindowsStoreInstallOptions=false&_=1571422283464
    // which seems to get consumer subscriptions, maybe another option?
    async function fetchSubscriptionInfo(userType) {
        var request = {
            method: 'GET',
            url: 'https://graph.microsoft.com/v1.0/me/licenseDetails',
            headers: {
                'X-UserType': userType
            }
        };

        var licenses = {};
        try {
            var response = await getUserService().httpRequest(request);

            for (const license of response.value.values()) {
                const sku = license.skuPartNumber.toLowerCase();
                var plans = license.servicePlans.filter(plan => plan.provisioningStatus === 'Success').map(plan => plan.servicePlanName.toLowerCase());
                licenses[sku] = plans;
            }
            return {'licenses': licenses};
        }
        catch (reason) {
            $log.error(`msidUser.fetchSubscriptionInfo failed: ${JSON.stringify(reason)}`);
            return;
        }
    }

    async function fetchOneDriveQuota() {
        var request = {
            method: 'GET',
            url: 'https://graph.microsoft.com/v1.0/me/drive',
            headers: {
                'X-UserType': type
            }
        };

        try {
            var response = await getUserService().httpRequest(request);
            var quota = parseInt(response.quota.total) / (1024 * 1024 * 1024);
            quota = Math.round(quota * 100) / 100;
            $log.info(`OneDrive quota: ${JSON.stringify(response.quota, null, '\t')} capacity: ${quota}GB`);
            return {'driveQuota': response.quota.total, 'driveQuotaString': `${quota}GB`};
        }
        catch (reason) {
            return Promise.reject(`msidUser.fetchOneDriveQuota failed: ${reason}`);
        }
    }

    function getResourceForEndpoint(endpoint) {
        if (!endpoint) {
            return scopes;
        }

        const endpointResources = {
            'https://graph.microsoft.com': {
                '/v1.0/me/': scopes,
                '/v1.0/me/drive/recent': 'Files.ReadWrite.All' //'https://graph.microsoft.com/Files.ReadWrite.All'
            },
            'https://nleditor.osi.office.net':
            {
                '/NlEditor/': 'https://nleditor.osi.office.net/NlEditor/instrumentation.write'
            }
        };

        try {
            const url = new URL(endpoint);
            if (!endpointResources.hasOwnProperty(url.origin)) {
                return scopes;
            }
            const paths = endpointResources[url.origin];
            const urlPath = url.pathname;

            var match = Object.keys(paths).reduce((previous, current) => {
                if (urlPath.startsWith(current) && current.length > previous.length) {
                    return current;
                } else {
                    return previous;
                }
            });

            return paths[match];
        }
        catch (reason) {
            $log.warning(`msidUser.Service threw an exception: ${reason}`);
            return scopes;
        }
    }

    async function requestAccessToken(resource, userInfo) {
        var additionalParams = {scope: resource, response_type: 'token'};
        if (userInfo && userInfo.email) {
            // additionalParams['domain_hint'] = organizations;
            additionalParams['login_hint'] = userInfo.email;
        }
        var params = createParams(additionalParams);
        var url = new URL(authorizeUrl);

        url.search = params.toString();
        var result = await getUserService().navigateToAuthEndpoint(url.href, type, true);

        if (!result || !result.access_token) {
            // eslint-disable-next-line no-debugger
            debugger;
            $log.info(`failed to get token from iframe for resource ${resource}`);
            return Promise.reject(`unable to retrieve result from ${url.href}`);
        }

        return result;
    }

    return {
        type: type,
        getConfig: getConfig,
        getResourceForEndpoint: getResourceForEndpoint,
        // handleLogin: handleLogin,
        lookupUserInfo: lookupUserInfo,
        requestAccessToken: requestAccessToken
    };
}
