import Utilities from '../common/utilities';
import './telemetry.service';
import constants from '../common/constants';
import {AWTEventProperties} from '@aria/webjs-sdk';

export default function ariaTelemetry($window) {
    var addEventProperties = function (eventProperties, properties) {
        if (Utilities.isUndefinedOrNull(eventProperties) || Utilities.isUndefinedOrNull(properties)) {
            return;
        }

        // Obfuscate pii properties out and remove
        if (!Utilities.isUndefinedOrNull(properties.pii)) {
            for (var i = 0; i <= properties.pii.length - 1; ++i) {
                eventProperties.setProperty(properties.pii[i].name, properties.pii[i].value, properties.pii[i].type);
            }
            delete properties.pii;
        }

        // Add rest of properties
        for (var property in properties) {
            if (properties.hasOwnProperty(property)) {
                eventProperties.setProperty(property, properties[property]);
            }
        }
    };

    var enabledSetting = true;

    this.setEnabledSetting = function (enabled) {
        enabledSetting = enabled;
    };

    this.trackEvent = function (eventName, properties, measurements) {
        if (!enabledSetting) {
            return;
        }

        // ARIA: Bug when dots are in event name; replace with underscores
        eventName = eventName.replace(/\./g, '_');

        var eventProperties = new AWTEventProperties();
        eventProperties.setName(constants.TELEMETRY.EVENT_PREFIX + eventName);
        addEventProperties(eventProperties, properties);
        addEventProperties(eventProperties, measurements);

        $window.ariaLogger.logEvent(eventProperties);
    };

    this.trackTrace = function (message, properties) {
        if (!enabledSetting) {
            return;
        }

        var eventProperties = new AWTEventProperties();
        eventProperties.setName(constants.TELEMETRY.EVENT_PREFIX + message);
        eventProperties.setProperty('severity', message);
        addEventProperties(eventProperties, properties);

        $window.ariaLogger.logEvent(eventProperties);
    };
}
