import BrowserHandler from '../common/browserHandler';
import constants from '../common/constants';
import Utilities from '../common/utilities';

// ppe = 'https://nleditor.osi.officeppe.net/NLEditor/BrowserExtension/V1'
const FlightingUrl = 'https://nleditor.osi.office.net/NLEditor/api/V1/BrowserExtension';

export default function userService($q, $log, $injector, storage, notification, msaUserService, o365UserService, msidUserService) {
    function authMessageListener(message, sender, response) {
        switch (message.activity) {
            case constants.ACTIVITY.LOGOUT.NAME:
                logout().then(() => {
                    response();
                });

                return true;
            case constants.ACTIVITY.AUTHENTICATION.NAME:
                login(message.type).then(result => {
                    response(result);
                });

                return true;
            case constants.ACTIVITY.AUTHORIZATION.NAME:
                {
                    // This handler can probably go away if it's determined that we don't
                    // need to call authenticate for any responses that aren't our own
                    if (sender.id === BrowserHandler.runtime.id && message.close) {
                        if (sender.tab && sender.tab.id) {
                            BrowserHandler.tabs.remove(sender.tab.id);
                        }
                        delete message.close;
                    }

                    authorize(unpackStateParam(message)).then(() => {
                        response();
                    });

                    return true;
                }
            case constants.ACTIVITY.REQUEST_TOKEN.NAME:
                {
                    navigateToAuthEndpoint(message.data.url, message.data.type, message.data.inIFrame).then(token => {
                        response(token);
                    });
                    return true;
                }
            default:
                {
                    return;
                }
        }
    }

    // This module can be loaded by pages other than the background page so only
    // handle these messages on the background page
    if (Utilities.isBackgroundContext()) {
        BrowserHandler.runtime.onMessage.addListener(authMessageListener);
    }

    async function acquireToken(userOrServiceType, endpoint, resource) {
        var userService = {};
        userService = await getServiceProvider(userOrServiceType);

        if (!userService) {
            $log.error('userService.acquireToken - Invalid type');
            return Promise.reject('Invalid userType');
        }

        if (!resource && userService.getResourceForEndpoint) {
            resource = userService.getResourceForEndpoint(endpoint);
        }

        var token = {};
        try {
            token = await getAccessToken(endpoint, resource);

            // if found cached token for resource/scope and not expired, return it
            if (token && token.access_token && !isTokenExpired(token.expires_on)) {
                $log.debug(`Using cached token with resource:'${resource}'${endpoint ? ' for ' + endpoint : ''} `);
                return token.access_token;
            }

            token = await requestAccessToken(userService, resource);

            if (token) {
                saveTokens(userService, endpoint, token);
            } else {
                return Promise.reject('No tokens available');
            }
        }
        catch (reason) {
            $log.error(`userService.acquireToken - failed. reason: ${reason}, userType: ${userOrServiceType}, endpoint: ${endpoint}, resource: ${resource}`);
        }

        if (!token || !token.access_token || !token.expires_on || isTokenExpired(token.expires_on)) {
            return null;
        }

        return token.access_token;
    }

    function getFunctionName() {
        var stack = Error().stack;
        if (stack.length < 3) {
            return;
        }
        var caller = stack.split('\n')[2];
        var match = caller.match(/^\s*at\s*(\w*)/);
        if (!match || match.length < 2) {
            return;
        }

        return match[1];
    }

    async function httpRequest(request) {
        if (!request) {
            return Promise.reject('Invalid Request');
        }

        var stack = new Error().stack;
        var result = new Promise((resolve, reject) => {
            const $http = $injector.get('$http');

            $http(request).then(
                (response) => {
                    resolve(response.data);
                },
                (response) => {
                    reject(`httpRequest failed for url: ${request.url} reason: ${response.statusText} stack:\n ${stack}`);
                });

        });

        return (await result);
    }

    async function acquireTokenFromCode(userService, code) {
        if (!userService) {
            $log.error('userService.acquireTokenFromCode - Invalid type');
            Promise.reject('Invalid userService type');
            return;
        }

        var config = userService.getConfig();
        config.params.grant_type = constants.OAUTH.AUTH_CODE;
        config.params.code = code;

        var request = {
            method: 'POST',
            url: config.tokenUrl,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
            },
            data: (new URLSearchParams(config.params)).toString()
        };

        const accessToken = await httpRequest(request);
        await saveTokens(userService, userService.getConfig().resource, accessToken);
        // is all the above needed or would protectedResourceInterceptor call into acquireToken
        // if the userType was just added to the header here?
        await lookupUserInfo(userService, accessToken);
        return accessToken.access_token;
    }

    async function getRefreshToken() {
        return getIdentityProperty('refresh_token');
    }

    async function refreshAccessToken(userService, resource, refresh_token) {
        if (!userService) {
            $log.error('userService.refreshAccessToken - Invalid type');
            return Promise.reject('Invalid userType');
        }

        var token = {};
        var config = userService.getConfig();

        config.params.grant_type = constants.OAUTH.REFRESH_TOKEN;
        config.params.resource = resource;
        config.params.refresh_token = refresh_token;

        var request = {
            method: 'POST',
            url: config.tokenUrl,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8',
            },
            data: (new URLSearchParams(config.params)).toString()
        };

        token = await httpRequest(request);
        return token;
    }

    async function requestAccessToken(userService, resource) {
        if (!userService) {
            $log.error('userService.requestAccessToken - Invalid type');
            return Promise.reject('Invalid userType');
        }

        // if there's a refresh token available (code grant only) attempt to renew it
        // if there is no refresh token then it's an implicit flow which just attempts
        // to get a new access token
        const refreshToken = await getRefreshToken();
        if (refreshToken) {
            return refreshAccessToken(userService, resource, refreshToken);
        }

        const userType = await getUserType();
        var userInfo = await getUserInfo(userType);
        if (userService.requestAccessToken) {
            var result = await userService.requestAccessToken(resource, userInfo);
        }

        return result;
    }

    async function authorize(request) {
        const serviceType = request.type;
        var userType = undefined;
        var userService = await getServiceProvider(serviceType);
        var result = false;
        if (!userService) {
            $log.error('userService.authenticate - Invalid type');
            return;
        }

        if (request.code) {
            userType = serviceType.userType;
            try {
                var token = await acquireTokenFromCode(userService, request.code);
                result = !!token;

            }
            catch (reason) {
                if (userType === constants.USER_TYPE.O365) {
                    notification.show(constants.NOTIFICATION.INVALIDSUBSCRIPTION);
                }
            }
        } else {

            if (request.id_token) {
                userType = await setUserType(request.id_token);
            }
            if (request.access_token) {
                await saveTokens(userService, userService.getConfig().userInfoUrl, request);
                if (!userType) {
                    userType = await getUserType();
                }
                await lookupUserInfo(userService, request.id_token);
            }
            result = userType !== constants.USER_TYPE.NONE;
        }
        if (request.error) {
            if (request.error === 'interaction_required') {
                login(await getUserType(), false);
            } else {
                $log.error(`userService.authenticate - authentication returned error: ${request.error} description: ${request.error_description} request: ${JSON.stringify(request)}`);
            }
        }

        $log.trackEvent('Authorization', {'userType': userType, 'serviceType': serviceType, 'succeeded': result});
        return result ? Promise.resolve() : Promise.reject();
    }

    async function clearToken() {
        return Promise.all([
            storage.remove('accessToken'),
            storage.remove('refreshToken'),
            storage.remove('userInfo'),
            storage.remove('userType'),
            storage.remove('identity'),
        ]);
    }

    async function getLoginStatus() {
        var result = await storage.get('userType');

        if (!result.userType) {
            return false;
        }
        try {
            var accessToken = await acquireToken(result.userType);
            return !!accessToken;
        }
        catch (reason) {
            return false;
        }
    }

    async function isLoggedIn() {
        var storageData = await storage.get(['userType', 'identity']);
        return (storageData.identity && storageData.userType && storageData.userType !== constants.USER_TYPE.NONE /*&& result.accessToken*/);
    }

    async function getUserType() {
        // Does this still need to be here? every telemetry call
        // calls this which causes us to log back in again
        /*
        var status = await getLoginStatus();
        if (!status) {
            deferred.resolve(constants.USER_TYPE.NONE);
            return deferred.promise;
        }
        */

        var result = await storage.get('userType');
        return result.userType || constants.USER_TYPE.NONE;
    }

    async function setUserType(typeOrToken) {
        var userType = '';
        if (Object.values(constants.USER_TYPE).includes(typeOrToken)) {
            userType = typeOrToken;
        } else {
            var tenantInfo = resolveTypeAndTenant(typeOrToken);
            userType = tenantInfo.type;
            delete tenantInfo.type;

            var tenantEntry = {
                [userType]: tenantInfo
            };

            var identity = await getStoredIdentity();

            mergeProperties(identity, tenantEntry);
            await storage.set({identity});
        }

        if (userType !== constants.USER_TYPE.NONE) {
            await storage.set({'userType': userType});
        }

        return userType;
    }

    function unpackStateParam(message) {
        // separate the type from the state param
        if (message.state) {
            message.state = decodeURIComponent(message.state);
        }
        var [state, type, appid] = message.state.split('|');
        if (type) {
            message.type = type;
        }

        return message;
    }

    async function navigateToAuthEndpoint(url, type, inIFrame) {
        return new Promise(async (resolve, reject) => {
            if (!Utilities.isBackgroundContext()) {
                BrowserHandler.runtime.sendMessage({activity: 'requestToken', data: {url: url, type: type, inIFrame: inIFrame}}, token => {
                    resolve(token);
                });
            } else {
                url = new URL(url);
                const params = url.searchParams;
                for (const param of ['state', 'nonce']) {
                    var value = params.get(param);
                    if (!value) {
                        value = createGuid();
                    }
                    // append the type to ensure the same is used to handle the response
                    if (param === 'state' && type) {
                        value += '|' + type + '|' + constants.APPINFO_NAME;
                    }

                    params.set(param, value);
                }

                var frameId = undefined;
                var timeout = undefined;
                // eslint-disable-next-line no-inner-declarations
                function cleanup() {
                    BrowserHandler.runtime.onMessage.removeListener(listener);
                    if (frameId) {
                        var iframe = document.getElementById(frameId);
                        if (iframe) {
                            iframe.parentNode.removeChild(iframe);
                        }
                    }
                }

                // remember the state parameter to unsure the response has the same state
                const state = params.get('state');
                const scope = params.get('scope');

                $log.info(`loading auth url:${url.href} ${inIFrame ? 'in iframe' : ''}`);
                var info = await getUserInfo();
                if (info && info.email && !params.has('login_hint')) {
                    params.set('login_hint', info.email);
                }

                // eslint-disable-next-line no-inner-declarations
                function listener(message, sender, response) {
                    if (sender.id !== BrowserHandler.runtime.id) {
                        $log.debug(`auth listener rejected invalid id: ${sender.id}`);
                        return;
                    }

                    if (message.activity === constants.ACTIVITY.AUTHORIZATION.NAME) {
                        var result = unpackStateParam(message);

                        if (!result) {
                            $log.error(`userService.listener - auth url:${url} returned null response `);
                            reject(result);
                        }

                        if (result.state !== state) {
                            $log.warn(`userService.listener - auth url:${url} mismatched state:${result.state}`);
                        }

                        $log[result.error ? 'error' : 'debug'](`userService.listener - auth response:${JSON.stringify(result)} received from url:${url}`);

                        if (timeout) {
                            clearTimeout(timeout);
                        }

                        delete result.activity;
                        cleanup();
                        resolve(result);
                    }
                }
                BrowserHandler.runtime.onMessage.addListener(listener);

                if (!inIFrame) {
                    BrowserHandler.tabs.create({url: url.toString()});
                } else {
                    params.set('prompt', 'none');
                    frameId = 'authFrame!' + scope;

                    const iframeAuthDelay = 60000;
                    timeout = setTimeout(() => {
                        $log.warn(`userService.authInFrame timed out waiting for ${url.toString()} after ${iframeAuthDelay} ms`);
                        cleanup();
                        reject('Timed out waiting for response from iFrame');
                    }, iframeAuthDelay);

                    var iframe = document.getElementById(frameId);
                    if (!iframe) {
                        iframe = document.createElement('iframe');
                        iframe.setAttribute('id', frameId);
                        iframe.style.visibility = "hidden";
                        iframe.style.position = "absolute";
                        iframe.style.width = iframe.style.height = "0";
                        iframe.style.border = "0";
                        iframe.setAttribute("sandbox", "allow-scripts allow-same-origin allow-forms");

                        document.body.appendChild(iframe);
                        iframe.src = url.toString();
                    }
                }
            }
        });
    }

    async function login(userType, clear = true) {
        if (clear === true) {
            clearToken();
        }

        var userService = await getServiceProvider(userType);
        if (userService.handleLogin) {
            return userService.handleLogin();
        }

        const config = userService.getConfig();
        var loginUrl = config['loginUrl'];
        if (!loginUrl) {
            $log.error('userService.login - Invalid type');
            return;
        }

        return navigateToAuthEndpoint(loginUrl, userService.type);
    }

    async function logout() {
        const userType = await getUserType();
        var userService = await getServiceProvider(userType);
        clearToken();
        if (userService.handleLogout) {
            await userService.handleLogout();
        } else {
            const config = userService.getConfig();
            var logoutUrl = config['logoutUrl'];
            if (!logoutUrl) {
                $log.error('userService.logout - Invalid type');
                return;
            }

            var request = {
                method: 'GET',
                url: logoutUrl
            };

            await httpRequest(request);
        }

        $log.debug(`userService.logout - ${userType}`);
    }

    async function getDeviceId() {
        var result = await storage.get('deviceId');
        if (result.deviceId) {
            return result.deviceId;
        }

        var id = createGuid();
        await storage.set({'deviceId': id});
        return id;
    }

    async function useAuthNext(toggle) {
        if (typeof toggle !== 'undefined') {
            storage.set({'authNext': {override: toggle}});
        }

        return checkAuthFlighting({noLogout: true});
    }

    window.useAuthNext = useAuthNext;
    window.checkTransitionAuth = checkAuthFlighting;

    // only check once a day
    const transitionAuthInterval = 24 /*hours*/ * 3600 /*seconds per hour*/ * 1000/* millseconds per second*/;

    async function initCheckTransition() {
        $log.debug('initCheckTransition');
        //check now before setting interval
        await checkAuthFlighting();
        // set the recurring interval to perform the check
        setInterval(checkAuthFlighting, transitionAuthInterval);
    }

    if (Utilities.isBackgroundContext()) {
        initCheckTransition();
    }

    async function checkAuthFlighting(options) {
        var now = new Date();
        const checkedTime = now.toUTCString();
        var storageData = await storage.get('authNext');
        now = Math.round(now / 1000);
        var elapsed = 0;
        if (storageData.authNext && storageData.authNext.lastCheck && Utilities.isNotUndefinedOrNull(storageData.authNext.flighted)) {
            elapsed = now - storageData.authNext.lastCheck;

            if (!(options && options.force === true) && elapsed < transitionAuthInterval && storageData.authNext.flighted) {
                return storageData.authNext.flighted;
            }
        }

        $log.info(`checkTransitionAuth time:${checkedTime}`);

        var authNext = await (async () => {
            // Guidance from service is to use deviceId as userId since the userId isn't
            // always available, and would change if they login with a different account.
            // X_UserId needs to be consistent so they don't jump flights but
            // X_CorrelationId isn't required
            const editorService = FlightingUrl;
            var deviceId = await getDeviceId();
            var request = {
                url: editorService,
                method: 'POST',
                accept: 'application/json',
                contentType: 'application/json',
                headers: {
                    // 'X-CorrelationId': deviceId,
                    'X-UserId': deviceId
                },
                data: {
                    AppId: "OfficeBX_Online"
                }
            };

            var result = {};
            try {
                result = await httpRequest(request);
            } catch (error) {
                // eslint-disable-next-line no-debugger
                debugger;
                $log.error(`userService.checkAuthFlighting - Call to editor service failed. error:${JSON.stringify(error)}`);
            }

            var enabled = false;
            if (result && result.FeatureFlags && result.FeatureFlags.authNext) {
                var authNext = result.FeatureFlags.authNext;
                enabled = typeof authNext === 'string' ? authNext.toLowerCase() === 'true' : !!authNext;
            }

            $log.info(`editor featureFlags.authNext returned:${enabled}`);
            return enabled;
        })();

        await storage.set({'authNext': {flighted: authNext, 'lastCheck': now}});

        // check the override last so the flighting service will still get called as usual
        if (storageData.authNext && Utilities.isNotUndefinedOrNull(storageData.authNext.override)) {
            $log.info(`using authNext override from local storage.`);
            return storageData.authNext.override;
        }

        var serviceType = await getIdentityProperty('service');
        if (authNext === (serviceType === constants.USER_SERVICE_TYPE.MSID)) {
            // current serviceType and authNext match so return with no change
            return;
        }

        $log.trackEvent('AuthNext', {elapsed, authNext, serviceType});

        // logout to force user to login with correct flighted auth
        if (options && !options.noLogout) {
            await logout();
        }
    }

    async function getLastAuthCheck() {
        if (!window.lastAuthFlightCheck) {
            var result = await storage.get('authNext');
            if (result.lastCheck) {
                window.lastAuthFlightCheck = result.lastCheck;
            }
        }
        return window.lastAuthFlightCheck;
    }


    async function getServiceForUserType(userType) {
        // if one serviceType was responsible for logged in identity return it
        var serviceType = await getIdentityProperty('service', userType);
        if (serviceType) {
            return serviceType;
        }

        // otherwise determine which to use
        serviceType = constants.USER_SERVICE_TYPE.NONE;
        if (await useAuthNext()) {
            return constants.USER_SERVICE_TYPE.MSID;
        }

        switch (userType) {
            case constants.USER_TYPE.MSA:
                serviceType = constants.USER_SERVICE_TYPE.MSA;
                break;
            case constants.USER_TYPE.O365:
                serviceType = constants.USER_SERVICE_TYPE.O365;
                break;
        }
        return serviceType;
    }

    async function getServiceProvider(type) {
        var userServiceProvider = null;
        if (!Object.values(constants.USER_SERVICE_TYPE).includes(type)) {
            type = await getServiceForUserType(type);
        }
        // Determine which user service to use and inject
        if (type.indexOf(constants.USER_SERVICE_TYPE.MSA) !== -1) {
            userServiceProvider = msaUserService;
        } else if (type.indexOf(constants.USER_SERVICE_TYPE.O365) !== -1) {
            userServiceProvider = o365UserService;
        } else if (type.indexOf(constants.USER_SERVICE_TYPE.MSID) !== -1) {
            userServiceProvider = msidUserService;
        } else {
            // eslint-disable-next-line no-debugger
            debugger;
            $log.error(`userService.getServiceProvider - Invalid type - ${type}`);
        }

        return userServiceProvider;
    }

    async function getStoredIdentity() {
        var result = await storage.get('identity');
        return (result && result.identity) ? result.identity : {};
    }

    function mergeProperties(target, source) {
        for (var [key, sourceValue] of Object.entries(source)) {
            if (!sourceValue) {
                continue;
            }

            if (!target[key] || target[key] === "none" || sourceValue === "none") {
                target[key] = sourceValue;
                continue;
            }

            if (typeof sourceValue !== typeof target[key]) {
                $log.warn(`skipping ${key} due to conflicting types`);
                continue;
            }

            if (Array.isArray(sourceValue)) {
                sourceValue.forEach((value) => {
                    var index = target[key].indexOf(value);
                    if (index < 0) {
                        target[key].push(value);
                    }
                });

                continue;
            }

            if (typeof sourceValue === 'object') {

                mergeProperties(target[key], sourceValue);
                continue;
            }
            target[key] = sourceValue;
        }
    }

    function resolveTypeAndTenant(tokenData) {
        var scopes = tokenData.scopes || tokenData.scope;
        if (scopes && scopes.includes('wl.')) {
            return {type: constants.USER_TYPE.MSA};
        }
        var idToken = tokenData.id_token || tokenData;
        if (!idToken) {
            return {type: constants.USER_TYPE.NONE};
        }

        const tenants = {
            '9188040d-6c67-4c5b-b112-36a304b66dad': {
                'type': constants.USER_TYPE.MSA, 'audience': 'prod'
            },
            '4925308c-f164-4d2d-bc7e-0631132e9375': {
                'type': constants.USER_TYPE.MSA, 'audience': 'ppe'
            },
            '72f988bf-86f1-41af-91ab-2d7cd011db47': {
                'type': constants.USER_TYPE.O365, 'audience': 'prod', 'domain': 'microsoft.com'
            },
            'f686d426-8d16-42db-81b7-ab578e110ccd': {
                'type': constants.USER_TYPE.O365, 'audience': 'ppe', 'domain': 'microsoft.com'
            }
        };

        try {
            const {header, payload} = parseJwtToken(idToken);
            var result = {['tenant']: payload.tid};
            if (tenants.hasOwnProperty(result.tenant)) {
                var tenantInfo = tenants[result.tenant];
                result = Object.assign(result, tenantInfo);
            } else {
                // everything not included in tenants would be AAD
                result.type = constants.USER_TYPE.O365;
            }

            return result;
        }
        catch (error) {
            // If it wasn't parsable then it must be an MSA
            return {type: constants.USER_TYPE.MSA};
        }
    }

    function getEndpointBase(endpoint) {
        const url = new URL(endpoint);
        return url.origin;
    }

    async function saveTokens(userService, endpoint, tokenData) {
        if (tokenData.expires_in && !tokenData.expires_on) {
            tokenData.expires_on = Utilities.getCurrentTime() + Number(tokenData.expires_in);
        }

        if (!endpoint) {
            $log.error('userService.saveTokens - endpoint not provided');
            return;
        }

        var userType = await getUserType();
        if (userType === constants.USER_TYPE.NONE) {
            userType = await setUserType(tokenData);
        }

        if (userType === constants.USER_TYPE.NONE) {
            $log.error('userService.saveTokens - unable to resolve userType');
            return;
        }

        var scope = decodeURIComponent(tokenData.scope).toLowerCase().split(/[ +]/);

        // if one of the scopes contains a url, then it is the actual endpoint
        for (const item of scope) {
            var result = item.match(/(^https:\/\/[.\w]*(?:\S+\/))/i);
            if (result) {
                endpoint = result[result.index];
                break;
            }
        }
        endpoint = getEndpointBase(endpoint);
        if (tokenData.resource && tokenData.resource !== endpoint) {
            $log.info(`saveTokens ${endpoint} does not match ${tokenData.resource}`);
            endpoint = tokenData.resource;
        }

        var tokenEntry = {
            [userType]: {
                "id_token": tokenData.id_token,
                "refresh_token": tokenData.refresh_token,
                "service": userService.type,
                "resources": {
                    [endpoint]: {
                        "access_token": tokenData.access_token,
                        "expires_in": tokenData.expires_in,
                        "expires_on": tokenData.expires_on,
                        "scope": scope
                    }
                }
            }
        };

        var identity = await getStoredIdentity();

        mergeProperties(identity, tokenEntry);
        await storage.set({identity});
    }

    function supportsScopes(required, granted) {
        if (!required) {
            return true;
        }

        if (!granted) {
            return false;
        }

        if (!Array.isArray(required)) {
            required = required.toLowerCase().split(/[ +]/);
        }

        for (const scope of required) {
            if (!granted.includes(scope)) {
                return false;
            }
        }
        return true;
    }

    async function getAccessToken(endpoint, resourceScope) {
        if (!endpoint) {
            $log.error('userService.getAccessToken - endpoint not provided');
            return;
        }

        endpoint = getEndpointBase(endpoint);

        try {
            var allTokens = await getIdentityProperty('resources');

            // either specifically by name or the first one if there is no entry for the endpoint
            if (allTokens[endpoint]) {

                var tokenData = allTokens[endpoint];
                // if the resource is the same as the endpoint as in the case of legacy auth,
                // no reason to validate the scopes. just return it
                if (endpoint === resourceScope) {
                    return tokenData;
                }

                if (tokenData && supportsScopes(resourceScope, tokenData.scope)) {
                    return tokenData;
                }

                // remove it from the array so it's not checked again below
                delete allTokens[endpoint];
            }

            // in case the token data was stored under a different endpoint, try to find the appropriate
            // scopes stored under other scopes
            for (const token in allTokens) {
                if (supportsScopes(resourceScope, token.scope)) {
                    return token;
                }
            }

            return null;
        }
        catch (error) {
            return null;
        }
    }

    async function getUserInfo(userType) {
        return getIdentityProperty('userInfo', userType);
    }

    async function getIdentityProperty(property, userType) {
        if (!userType) {
            userType = await getUserType();
        }

        var identity = await getStoredIdentity();

        if (!identity[userType] || !identity[userType][property]) {
            return null;
        }

        return identity[userType][property];
    }

    // Waits for the userInfo to be available (non-null) before resolving
    async function waitForUserInfo(userType) {
        return new Promise(async (resolve) => {
            var userInfo = await getUserInfo(userType);
            // userInfo is available => resolve
            if (Utilities.isNotUndefinedOrNull(userInfo)) {
                return resolve(userInfo);
            }

            BrowserHandler.runtime.onMessage.addListener(userInfoListener);

            // in testing mode, only wait for a short time before giving up on getting the userInfo so as not to slow down unit tests too much
            var giveUpDelay = Utilities.isExtensionInTestingMode() ? constants.TIMEOUT.USER_INFO_LOOKUP_TEST : constants.TIMEOUT.USER_INFO_LOOKUP;

            // if looking up the userInfo takes too long, resolve with the current userInfo (undefined or null)
            var giveUp = setTimeout(() => {
                $log.warn(`userService.waitForUserInfo timed out after ${constants.TIMEOUT.USER_INFO_LOOKUP} ms - ${userType}`);
                cleanUp();
                resolve(userInfo);
            }, giveUpDelay);

            // listen for a runtime message that is sent when the userInfo is available from lookupUserInfo
            function userInfoListener(request) {
                if (request.activity === constants.ACTIVITY.USERINFO_AVAILABLE.NAME) {
                    cleanUp();
                    clearTimeout(giveUp);
                    resolve(request.data);
                }
            }

            function cleanUp() {
                BrowserHandler.runtime.onMessage.removeListener(userInfoListener);
            }
        });
    }

    async function saveUserInfo(userType, data, replace) {

        var identity = await getStoredIdentity();

        var userInfo = {};
        if (identity[userType] && identity[userType].userInfo && !replace) {
            userInfo = identity[userType].userInfo;
        }

        mergeProperties(userInfo, data);

        if (identity[userType]) {
            identity[userType].userInfo = userInfo;
            await storage.set({identity});
        }
    }

    function isTokenExpired(expiresOn) {
        var now = Utilities.getCurrentTime();
        var offset = 120;
        return expiresOn && expiresOn > now + offset ? false : true;
    }

    async function lookupUserInfo(userService, tokenData) {
        var userType = await getUserType();

        var userInfo = await userService.lookupUserInfo(tokenData);
        // send a message (listened to in waitForUserInfo) with the newly available userInfo
        BrowserHandler.runtime.sendMessage({activity: constants.ACTIVITY.USERINFO_AVAILABLE.NAME, data: userInfo});
        // save the userInfo to local storage
        if (!userInfo.email && userInfo.userPrincipalName) {
            userInfo.email = userInfo.userPrincipalName;
        }

        await saveUserInfo(userType, userInfo, true);

        // get user's profile photo
        await getPhotoFromServer(userService);
    }

    // TODO: consolidate with o365User once we have WebPack
    function parseJwtToken(token) {
        var idTokenPartsRegex = /^([^.\s]*)\.([^.\s]+)\.([^.\s]*)$/;

        var matches = idTokenPartsRegex.exec(token);
        if (!matches || matches.length < 4) {
            $log.error('userService.parseJwtToken - The returned id_token is not parseable.');
            return null;
        }

        var header = JSON.parse(base64DecodeStringUrlSafe(matches[1]));
        var payload = JSON.parse(base64DecodeStringUrlSafe(matches[2]));
        return {header, payload};
    }

    function base64DecodeStringUrlSafe(base64IdToken) {
        base64IdToken = base64IdToken.replace(/-/g, '+').replace(/_/g, '/');
        return decodeURIComponent(escape(window.atob(base64IdToken)));
    }

    // Gets the user's photo (profile picture) from either local storage or an http call
    // Calls the helper functions attached to the service (getUserType, getUserInfo, getPhotoFromServer) to allow for mocking in UTs
    async function getUserPhoto() {
        var userType = await getUserType();
        // Invalid user type - no photo
        if (userType === constants.USER_TYPE.NONE) {
            $log.warn('UserService.getUserPhoto: no signed-in user');
            return Promise.reject(constants.RECENTS.ERROR.UNSUPPORTED_USER_TYPE);
        }

        var userInfo = await getUserInfo(userType);
        // If there is a photo in local storage, use that
        if (userInfo && userInfo.photo) {
            $log.trackEvent('UserService_GotLocalPhoto', {'userType': userType});
            return userInfo.photo;
        }

        // No local photo - Do not retry to fetch the photo from server, use the default photo instead.
        return Promise.reject('No photo in local storage');
    }

    // Gets the user's photo from the appropriate endpoint based on the given userType
    async function getPhotoFromServer(userService) {
        const serviceType = userService.type;
        const userType = await getUserType();
        const config = userService.getConfig();
        var photoUrl = config['photoUrl'];
        if (!photoUrl) {
            return;
        }

        // Request to get photo
        var photoRequest = {
            method: 'GET',
            url: photoUrl,
            responseType: 'blob',
            headers: {
                'X-UserType': serviceType
            }
        };

        // Send photo request
        try {
            const photo = await httpRequest(photoRequest);
            $log.trackEvent('UserService_GotServerPhoto', {'userType': userType});

            // Convert the photo response into a displayable string source
            var reader = new FileReader();
            reader.onload = async () => {

                // Save the photo in the userInfo in local storage
                saveUserInfo(userType, {photo: reader.result});

                return reader.result;
            };
            reader.readAsDataURL(photo);
        }
        catch (error) {
            return null;
        }

    }

    async function getEndpointUrlForCurrentUser(endpoint) {
        const userType = await getUserType();
        if (!userType || userType === constants.USER_TYPE.NONE) {
            return null;
        }
        var userService = await getServiceProvider(userType);
        if (!userService) {
            return null;
        }

        const config = userService.getConfig();
        return config[endpoint];
    }

    function createGuid() {
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (char) {
            let timestamp = performance.now();
            const rand = (timestamp + Math.random() * 16) % 16 | 0;
            timestamp = Math.floor(timestamp / 16);
            return (char === 'x' ? rand : (rand & 0x3 | 0x8)).toString(16);
        });
    }

    async function getUserLicenseInfo() {
        var userInfo = await getUserInfo();
        const licenseProps = ['driveQuota', 'licenses'];
        return licenseProps.reduce((previous, current) => {
            if (userInfo[current]) {
                previous[current] = userInfo[current];
            }
            return previous;
        }, {});
    }

    // all exports are async
    return {
        acquireToken,
        clearToken,
        isLoggedIn,
        getEndpointUrlForCurrentUser,
        getUserType,
        getUserInfo,
        getUserPhoto,
        httpRequest,
        navigateToAuthEndpoint,
        saveUserInfo,
        waitForUserInfo,
        getUserLicenseInfo,
        useAuthNext,
        // Exposed to be called from UTs
        authorize,
        getAccessToken,
        getPhotoFromServer,
        login,
        logout,
        lookupUserInfo,
    };
}
