import constants from './constants';
import Utilities from './utilities';
import 'angular';

export default httpInterceptor;

function httpInterceptor($q, $log, $timeout, $injector) {
    var retries = 0;
    return {
        request: function (config) {
            if (Utilities.isUndefinedOrNull(config.timeout)) {
                config.timeout = constants.TIMEOUT.DEFAULT_REQUEST;
            }
            return config;
        },
        responseError: function (response) {
            if (response) {
                switch (response.status) {
                    case 0:
                        $log.debug(`httpErrorResponseInterceptor - Request cancelled - ${response.config.url}`);
                        break;
                    case 401:
                        $log.info(`httpErrorResponseInterceptor - ${response.status} - ${response.config.url} - ${JSON.stringify(response.data)}`);
                        break;
                    case 400:
                        // Error of invalid AADService. Log the info trace.
                        if (
                            Utilities.isNotUndefinedOrNull(response.data) &&
                            Utilities.isNotUndefinedOrNull(response.data.error_codes) &&
                            response.data.error_codes[0] === 50001
                        ) {
                            $log.info(`httpErrorResponseInterceptor -  Invalid AADSerivce - ${response.status} - ${JSON.stringify(response.data)}`);
                            break;
                        }

                        // Interaction required. Retry the request with the same resource for 3 times.
                        // https://docs.microsoft.com/en-us/azure/active-directory/develop/active-directory-protocols-oauth-code
                        if (
                            Utilities.isNotUndefinedOrNull(response.data) &&
                            Utilities.isNotUndefinedOrNull(response.data.error_codes) &&
                            response.data.error_codes[0] === 50079
                        ) {
                            $log.info(`httpErrorResponseInterceptor -  Interaction required - ${response.status} - ${JSON.stringify(response.data)}`);
                            if (retries < constants.ERROR_RETRIES.HTTP_REQUEST) {
                                retries++;
                                return $timeout(function () {
                                    var $http = $injector.get('$http');
                                    return $http(response.config);
                                }, constants.TIMEOUT.USER_INFO_LOOKUP);
                            }

                            retries = 0;
                        }
                        break;
                    case 404:
                        // don't throw exceptions when graph returns something not found
                        if (response.config.url.startsWith('https://graph.microsoft.com/v1.0/me/drive')) {
                            return $q.resolve(response);
                        }
                    // eslint-disable-next-line no-fallthrough
                    default:
                        $log.error(`httpErrorResponseInterceptor - ${response.status} - ${response.config.url} - ${JSON.stringify(response.data)}`);
                }
            }

            return $q.reject(response);
        },
    };
}
