import Utilities from '../common/utilities';
import constants from '../common/constants';

export default mruService;

function mruService($q, $log, $http, userService) {
    // Gets the most recent documents list from either OneDrive or o365
    async function getRecentDocumentList() {
        const userType = await userService.getUserType();
        if (userType === constants.USER_TYPE.NONE) {
            return Promise.reject(constants.RECENTS.ERROR.UNSUPPORTED_USER_TYPE);
        }
        const documentsUrl = await userService.getEndpointUrlForCurrentUser('documentsUrl');
        var recentDocs = await getRecentDocs(userType, documentsUrl);
        await mergeSharedDocs(userType, recentDocs);
        return normalizeMruList(recentDocs.data || recentDocs.value || recentDocs);
    }

    // Calls into the appropriate endpoint to retrieve the recent documents
    async function getRecentDocs(userType, endpointUrl) {
        return new Promise((resolve, reject) => {
            var request = {
                method: 'GET',
                accept: 'application/json',
                url: endpointUrl,
                contentType: 'application/json',
                headers: {
                    'X-UserType': userType
                }
            };

            $http(request).then(
                function (response) {
                    resolve(response.data);
                },
                function () {
                    reject(constants.RECENTS.ERROR.GENERIC);
                });
        });
    }

    async function getOneDriveItem(userType, itemId) {
        var request = {
            method: 'GET',
            accept: 'application/json',
            url: `https://graph.microsoft.com/v1.0/me/drive/items/${itemId}`,
            contentType: 'application/json',
            headers: {
                'X-UserType': userType
            }
        };
        try {
            return new Promise(resolve => {
                $http(request)
                    .then(function (response) {
                        resolve(response.data);
                    });
            });
        }
        catch (_) {
            return Promise.resolve({});
        }
    }

    async function getOneDriveSharedItems(userType) {
        var request = {
            method: 'GET',
            accept: 'application/json',
            url: 'https://graph.microsoft.com/v1.0/me/drive/sharedWithMe',
            contentType: 'application/json',
            headers: {
                'X-UserType': userType,
            },
        };
        return new Promise(resolve => {
            try {
                $http(request)
                    .then(function (response) {
                        resolve(response.data);
                    });
            }
            catch (_) {
                resolve({});
            }
        });
    }

    // When using msa msgraph, recent docs doesn't include the necessary
    // metadata needed to sort and access the files, so we have to get the
    // documents shared with the person to look up the info for any shared
    // documents and then each individual id for those owned by the person
    async function mergeSharedDocs(userType, recentDocs) {
        if (!recentDocs.value) {
            return;
        }

        var sharedItems = undefined;
        for (var recent of recentDocs.value) {
            if (Object.values(recent.remoteItem).length) {
                continue;
            }

            // only make this call if any of the recent docs
            // don't have the remoteItem data
            if (!sharedItems) {
                var result = await getOneDriveSharedItems(userType);
                if (!result.value) {
                    return;
                }
                sharedItems = result.value;
            }

            var shared = sharedItems.find((value) => {
                return value.remoteItem && value.remoteItem.id && value.remoteItem.id.toLowerCase() === recent.id.toLowerCase();
            });

            if (shared) {
                recent.remoteItem = shared.remoteItem;
                continue;
            }

            var item;
            try {
                item = await getOneDriveItem(userType, recent.id);
            } catch (_) {
                continue;
            }

            if (!item || !item.id) {
                continue;
            }

            recent.remoteItem.webUrl = item.webUrl;
            recent.name = item.name;
            if (!recent.file) {
                recent.file = item.file;
            }
            if (!recent.remoteItem.package) {
                recent.remoteItem.package = item.package;
            }
            if (!recent.remoteItem.lastModifiedDateTime) {
                recent.remoteItem.lastModifiedDateTime = item.lastModifiedDateTime;
            }
        }
    }

    const supportedMimeType = {
        'application/vnd.openxmlformats-officedocument.wordprocessingml.document': 'word',
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet': 'excel',
        'application/vnd.openxmlformats-officedocument.presentationml.presentation': 'powerpoint',
        'application/msonenote': 'onenote'
    };

    // Helper function to normalize the MRU list to be in the form of the JSON returned by MRU++ (with just the values we use)
    // TODO: Once we use MRU++ for both services, we won't need this any longer
    function normalizeMruList(fileList) {
        var newFileList = [];
        if (Utilities.isUndefinedOrNull(fileList)) {
            return newFileList;
        }

        for (const entry of fileList) {
            // if it's already got the correct metadata just add it and continue
            if (entry.DocumentUrl && entry.Timestamp && entry.Application && entry.FileName/* && entry.Id*/) {
                newFileList.push(entry);
                continue;
            }

            // if it doesn't have the other known fields, then move along to the next
            if (!entry.remoteItem) {
                continue;
            }

            const fileName = entry.name || entry.remoteItem.name;
            const id = entry.id || entry.remoteItem.id;
            if (!fileName || !id) {
                continue;
            }

            var remoteItem = entry.remoteItem;
            var documentUrl = undefined;
            var timestamp = undefined;
            var application = undefined;
            if (remoteItem.mru) {
                var mruItem = remoteItem.mru;
                if (mruItem.app && constants.FILE.SUPPORTED_APPLICATIONS.indexOf(mruItem.app) !== -1) {
                    documentUrl = mruItem.url;
                    timestamp = mruItem.timeStamp;
                    application = mruItem.app.toLowerCase();
                }
            }
            else {
                // look in other known locations for url
                documentUrl = remoteItem.webDavUrl || remoteItem.webUrl;
                if (!documentUrl) {
                    continue;
                }

                const supportedExtensions = constants.FILE.SUPPORTED_FILE_TYPES;
                var ext = Utilities.getFileExtension(documentUrl) || Utilities.getFileExtension(fileName);
                if (supportedExtensions.hasOwnProperty(ext)) {
                    application = supportedExtensions[ext];
                } else if (remoteItem.file && remoteItem.file.mimeType && supportedMimeType.hasOwnProperty(remoteItem.file.mimeType)) {
                    application = supportedMimeType[remoteItem.file.mimeType];
                } else if (!application) {
                    var packageInfo = remoteItem.package || entry.package;
                    if (!packageInfo || !packageInfo.type) {
                        continue;
                    }
                    application = packageInfo.type.toLowerCase();
                    if (!Object.values(supportedExtensions).includes(application)) {
                        continue;
                    }
                }
                if (!application) {
                    continue;
                }

                if (remoteItem.lastModifiedDateTime) {
                    timestamp = remoteItem.lastModifiedDateTime;
                } else if (remoteItem.fileSystemInfo) {
                    timestamp = remoteItem.fileSystemInfo.lastAccessedDateTime || remoteItem.fileSystemInfo.lastModifiedDateTime;
                }

                if (!timestamp) {
                    continue;
                }
            }

            newFileList.push({
                DocumentUrl: documentUrl,
                Timestamp: timestamp,
                Application: application,
                FileName: fileName,
                Id: id
            });
        }
        return newFileList;
    }

    var service = {
        getRecentDocumentList: getRecentDocumentList,

        // Exposed to be called from UTs
        getRecentDocs: getRecentDocs,
        normalizeMruList: normalizeMruList,
    };

    return service;
}
