/* global Utilities */
import '../common/utilities';
import './experimentConstants';
import constants from '../common/constants';

export default function experimentService($http, $q, $log, experimentConstants, userService) {
    // Private variables
    var flights = null;
    var flightsPromise = null;

    var userTypeMap = {[constants.USER_TYPE.MSA]: 'MSA', [constants.USER_TYPE.O365]: 'O365'};

    var currentUserType;

    // Exposed methods
    var service = {
        getFlight: getFlight,

        // Exposed to call from UTs
        getFlights: getFlights,
        flightingDisabled: flightingDisabled,
        experimentEnabled: experimentEnabled,
    };

    return service;

    // Function declarations

    // Returns the flight assignment for the experiment with the given name
    async function getFlight(expName) {
        var enabled = await experimentEnabled(expName);
        if (!enabled) {
            return experimentConstants.EXPERIMENTS[expName].CONTROL;
        }

        var flights = await getFlights();
        return flights[expName];
    }

    // Returns all flight assignments
    async function getFlights() {
        if (Utilities.isNotUndefinedOrNull(flights)) {
            return flights;
        }

        if (Utilities.isNotUndefinedOrNull(flightsPromise)) {
            await Promise.all([flightsPromise]);
            return flights;
        }

        await setFlights();
        return flights;
    }

    // Resolves with true iff the experiment with the given name is enabled
    async function experimentEnabled(expName) {
        try {
            var userType = await userService.getUserType();
            // if all flighting is disabled, the experiment is disabled (global experiment enabled settings override individual experiment enabled settings)
            if (service.flightingDisabled(userType)) {
                return false;
            }

            return !disabledCheck(userType, expName);

        } catch (error) {
            $log.error(`experimentService.experimentEnabled error: ${error}`);
            return false;
        }
    }

    // Returns true iff flighting is disabled for the given user type or the current browser
    function flightingDisabled(userType) {
        return disabledCheck(userType);
    }

    // Sets the private flights variable by getting the user-assigned features from local storage or ExP
    async function setFlights() {
        try {
            var userType = await userService.getUserType();
            currentUserType = userTypeMap[userType];

            // undefined, null, or wrong userType => default flights
            if (Utilities.isUndefinedOrNull(userType) || userType === constants.USER_TYPE.NONE) {
                throw (experimentConstants.ERROR.BAD_USER_TYPE);
            }

            // flighting is disabled for the current user => default flights
            if (service.flightingDisabled(userType)) {
                flights = defaultFlights();
                return;
            }

            var userInfo = await userService.waitForUserInfo(userType);
            // if there are flights in the local userInfo, use those
            if (Utilities.isNotUndefinedOrNull(userInfo) && Utilities.isNotUndefinedOrNull(userInfo.flights)) {
                flights = userInfo.flights;
                return;
            }

            // get the flight information from the ExP service and save in userInfo
            var clientId = getClientId(userType, userInfo);
            var features = await getFeatures(clientId);
            featuresToFlights(features);
            userInfo.flights = flights;
            userInfo.features = features;
            await userService.saveUserInfo(userType, userInfo);
        } catch (error) {
            // if any error occurs while getting the flight assignments, set the flights to default (all controls) and resolve
            $log.error(`experimentService.setFlights error: ${error}`);
            flights = defaultFlights();
        }

    }

    // Returns the clientId from the given userInfo:
    // cid (msa users) or the default specified in experimentConstants
    function getClientId(userType, userInfo) {
        if (Utilities.isUndefinedOrNull(userInfo)) {
            return experimentConstants.DEFAULT_CLIENT_ID;
        }

        // TODO: does this still do anything. if so is the id the same as the cid?
        var cid = userInfo.cid || userInfo.id;
        if (cid) {
            return cid;
        }

        return experimentConstants.DEFAULT_CLIENT_ID;
    }

    // Gets the user features array from the ExP flighting service, passing in the given clientId
    function getFeatures(clientId) {
        var deferral = $q.defer();
        // bad clientId => no ExP features (reject)
        // var promise = new Promise((resolve, reject) => {
        if (Utilities.isUndefinedOrNull(clientId) || clientId === experimentConstants.DEFAULT_CLIENT_ID) {
            $log.error('experimentService.getFeatures: clientId is undefined, null, or empty');
            return deferral.reject(experimentConstants.ERROR.NO_CLIENT_ID);
        }

        var id = getTruncatedString(stringToHex(clientId));
        var request = {
            method: 'GET',
            url: experimentConstants.ENDPOINT,
            headers: {
                'X-MSEDGE-CLIENTID': id
            }
        };

        $http(request).then(
            (response) => {
                var result = response.data;
                if (result.Features) {
                    return deferral.resolve(result.Features);
                } else {
                    return deferral.reject(experimentConstants.ERROR.NO_FEATURES);
                }
            },
            () => {
                return deferral.reject(experimentConstants.ERROR.NO_FEATURES);
            });
        return deferral.promise;
    }

    // Sets the private flights variable to map each experiment name to the markup for the flight that the user has been assigned to for that experiment
    function featuresToFlights(features) {
        flights = {};
        for (var expKey in experimentConstants.EXPERIMENTS) {
            if (experimentConstants.EXPERIMENTS.hasOwnProperty(expKey)) {
                setExperimentFlights(features, expKey);
            }
        }

        // check for any invalid features from the ExP service
        validateFeatures(features);
    }

    // Helper function: assigns the given experiment key to the appropriate flight for the experiment
    function setExperimentFlights(features, expKey) {
        var experiment = experimentConstants.EXPERIMENTS[expKey];
        flights[expKey] = experiment.CONTROL;

        if (!experiment.ENABLED[currentUserType]) {
            return;
        }

        for (var flightKey in experiment.FLIGHTS) {
            if (experiment.FLIGHTS.hasOwnProperty(flightKey)) {
                if (features.indexOf(flightKey) > -1) {
                    flights[expKey] = experiment.FLIGHTS[flightKey];
                    break;
                }
            }
        }
    }

    // Sets the private flights variable to map each experiment name to its control
    // Used in case of any error in getting the actual flight assignments.
    function defaultFlights() {
        var flights = {};
        for (var expKey in experimentConstants.EXPERIMENTS) {
            if (experimentConstants.EXPERIMENTS.hasOwnProperty(expKey)) {
                var experiment = experimentConstants.EXPERIMENTS[expKey];
                flights[expKey] = experiment.CONTROL;
            }
        }
        return flights;
    }

    // Checks whether any feature in the given features array (obtained from ExP) is invalid, i.e. not part of any experiment feature->flight mapping
    // and logs an error if any invalid features are found
    function validateFeatures(features) {
        var invalid = [];
        for (var i = 0; i < features.length; ++i) {
            var feature = features[i];
            if (isFeatureInvalid(feature)) {
                invalid.push(feature);
            }
        }

        if (invalid.length > 0) {
            $log.warn(`ExP service returned invalid features: ${JSON.stringify(invalid)}`);
        }
    }

    // Helper function that returns true iff the given feature is invalid, i.e. is not a key in any experiment FLIGHTS mapping
    function isFeatureInvalid(feature) {
        for (var expKey in experimentConstants.EXPERIMENTS) {
            if (experimentConstants.EXPERIMENTS.hasOwnProperty(expKey)) {
                var experiment = experimentConstants.EXPERIMENTS[expKey];
                if (experiment.FLIGHTS.hasOwnProperty(feature)) {
                    return false;
                }
            }
        }

        return true;
    }

    // Helper function that returns true iff
    //  all flighting is disabled (if given an undefined or invalid expName)
    //  the experiment with the given name is disabled (if given a valid expName)
    function disabledCheck(userType, expName) {
        // no user type => flighting is disabled
        if (userType === constants.USER_TYPE.NONE) {
            return true;
        }

        // determine whether to check the enabled settings for all experiments or for a specific experiment
        var enabledSettings = experimentConstants.EXPERIMENTS.hasOwnProperty(expName) ? experimentConstants.EXPERIMENTS[expName].ENABLED : experimentConstants.ENABLED;
        var logName = expName ? `experiment ${expName} is` : 'all experiments are';

        // check whether flighting is disabled for the current browser - if so, flighting is disabled no matter the userType
        var browser = Utilities.getBrowserName().toLowerCase();
        var enabledBrowsers = enabledSettings.BROWSERS.toString().toLowerCase();
        if (enabledBrowsers.indexOf(browser) < 0) {
            $log.info(`experimentService: ${logName} disabled for browser ${browser}. Experiment is only enabled for browsers ${enabledBrowsers}`);
            return true;
        }

        // valid user type and enabled browser => use the setting for the userType
        var enabledForUser = enabledSettings[userTypeMap[userType]];
        if (!enabledForUser) {
            $log.info(`experimentService: ${logName} disabled for userType ${userType}`);
        }
        return !enabledForUser;
    }

    // Helper function that returns a hex representation of the given string (ExP expects the clientId to be in hex format)
    // Source: http://stackoverflow.com/questions/21647928/javascript-unicode-string-to-hex
    function stringToHex(string) {
        var hex, i;
        var result = '';

        for (i = 0; i < string.length; i++) {
            hex = string.charCodeAt(i).toString(16);
            result += ('000' + hex).slice(-4);
        }

        return result;
    }

    // Helper function used to ensure that the clientId sent to ExP is <= 100 characters (ExP clientId length limit)
    function getTruncatedString(string) {
        return string.length > 100 ? string.substring(0, 100) : string;
    }
}
