import angular from 'angular';
import constants from './experimentConstants';
import experimentService from './experiment.service';
import sharingController from './sharing/sharing.controller';
import sharingService from './sharing/sharing.service';
import SharingAController from './sharing/sharingA/sharingA.controller';

export default angular
    .module('app.experimentation', [])
    .constant('experimentConstants', constants)
    .factory('experimentService', ['$http', '$q', '$log', /* 'constants', */ 'experimentConstants', 'userService', experimentService])
    .controller('SharingController', ['experimentService', sharingController])
    .factory('sharingService', ['$http', '$q', '$log', /* 'constants', */ 'userService', sharingService])
    .controller('SharingAController', ['$log', 'sharingService', 'userService', 'localize', SharingAController])
    .name;
