import constants from '../common/constants';

export default function msaUserService($log, $injector) {
    this.type = constants.USER_TYPE.MSA;
    this.type = constants.USER_SERVICE_TYPE.MSA;
    this.userType = constants.USER_TYPE.MSA;
    this.getConfig = function () {
        var config = {
            'tokenUrl': constants.MSACONFIG.URL,
            'params': {
                'client_id': constants.MSACONFIG.CLIENT_ID,
                'client_secret': constants.MSACONFIG.CLIENT_SECRET,
                'redirect_uri': constants.MSACONFIG.REDIRECT_URI
            },
            'resource': constants.ONEDRIVE.INSTANCE,
            'loginUrl': constants.MSACONFIG.INSTANCE + '?response_type=code&client_id=' + constants.MSACONFIG.CLIENT_ID + '&redirect_uri=' + constants.MSACONFIG.REDIRECT_URI + '&scope=' + constants.MSACONFIG.SCOPE,
            'logoutUrl': constants.MSACONFIG.LOGOUT_URI,
            'photoUrl': constants.MSACONFIG.PHOTO_URL,
            'documentsUrl': constants.RECENTS.ONEDRIVE_ENDPOINT
        };

        return config;
    };

    this.lookupUserInfo = async function (data) {
        return new Promise((resolve, reject) => {
            var userInfo = {
                oneDriveUrl: constants.LINKS.APP.ONEDRIVE
            };

            var $http = $injector.get('$http');
            var request = {
                method: 'GET',
                url: constants.MSACONFIG.USERINFO_URL,
                headers: {
                    'X-UserType': constants.USER_SERVICE_TYPE.MSA
                }
            };

            $http(request).then(
                function (response) {
                    var data = response.data;
                    userInfo.email = data.emails.account;
                    userInfo.cid = data.id;
                    resolve(userInfo);
                },
                function () {
                    reject(`msaUser.Service.lookupUserInfo http request failed`);
                });
        });
    };

    this.getResourceForEndpoint = function (endpoint) {
        return constants.MSACONFIG.SCOPE;
    };
}
