import Utilities from './utilities';
import BrowserHandler from './browserHandler';
import constants from './constants';

export default function ($log, localize) {
    return {
        show: function (notification) {
            // Send message to background instance to handle show notification.
            if (!Utilities.isBackgroundContext()) {
                BrowserHandler.runtime.sendMessage({
                    activity: constants.ACTIVITY.NOTIFICATION.NAME,
                    notification: notification,
                });

                return;
            }

            var buttons;
            if (notification.buttons !== undefined && notification.buttons !== null) {
                buttons = [];
                for (var index = 0; index < notification.buttons.length; ++index) {
                    buttons[index] = {};
                    buttons[index].title = localize.getLocalizedString(notification.buttons[index].title);
                    buttons[index].iconUrl = notification.buttons[index].iconUrl;
                }
            }

            BrowserHandler.notifications.clear(notification.id, function (wasCleared) {
                BrowserHandler.notifications.create(
                    notification.id,
                    {
                        iconUrl: '../assets/icon.png',
                        imageUrl: notification.imageUrl,
                        message: localize.getLocalizedString(notification.message),
                        priority: notification.priority,
                        progress: notification.progress,
                        title: '',
                        type: notification.type,
                        buttons: buttons,
                    },
                    function (notificationId) {
                        if (notificationId === undefined || notificationId === null) {
                            $log.error('CreateNotificationError');
                        }
                    }
                );
            });
        },
    };
}
