export default function SharingController(experimentService) {
    var self = this;

    // Private properties
    var path = '/experimentation/sharing/';
    var name = 'SHARING';

    // Exposed properties
    self.flightView = path + 'sharingControl/sharingControl.html';

    initialize();

    function initialize() {
        experimentService.getFlight(name).then(function (flight) {
            self.flightView = path + flight;
        });
    }

}
