import angular from 'angular';
import './browserHandler'; /* eslint-disable-line sort-imports */
import 'angularjs-localizationservice.1.0.1/localize';
import httpInterceptor from './httpInterceptor';
import logDecorator from './logDecorator';
import notification from './notification.service';
import storage from './storage.service';

var commonModule = angular
    .module('app.common', [])
    .config(['$httpProvider', '$provide', appCommon])
    .factory('storage', ['$q', '$log', storage])
    .factory('notification', ['$log', 'localize', notification])
    .factory('httpInterceptor', ['$q', '$log', '$timeout', '$injector', httpInterceptor]);

export default commonModule.name;

function appCommon($httpProvider, $provide) {
    $provide.decorator('$log', ['$delegate', 'telemetry', logDecorator]);

    // Register http interceptors
    $httpProvider.interceptors.push('httpInterceptor');
}
