import Utilities from '../common/utilities';
import constants from '../common/constants';

export default function o365UserService($log, $injector, notification) {
    this.type = constants.USER_SERVICE_TYPE.O365;
    this.userType = constants.USER_TYPE.O365;
    this.getConfig = function () {
        var config = {
            'tokenUrl': constants.O365CONFIG.INSTANCE + 'token',
            'params': {
                'client_id': constants.O365CONFIG.CLIENT_ID,
                'client_secret': constants.O365CONFIG.CLIENT_SECRET,
                'redirect_uri': constants.O365CONFIG.REDIRECT_URI,
                'resource': constants.O365CONFIG.GRAPH_RESOURCE
            },
            'resource': constants.O365CONFIG.GRAPH_RESOURCE,
            'loginUrl': constants.O365CONFIG.INSTANCE + 'authorize?response_type=code&client_id=' + constants.O365CONFIG.CLIENT_ID + '&redirect_uri=' + constants.O365CONFIG.REDIRECT_URI + '&response_mode=query',
            'logoutUrl': constants.O365CONFIG.LOGOUT_URI,
            'photoUrl': constants.O365CONFIG.PHOTO_URL,
            'documentsUrl': constants.O365CONFIG.MRU_URL + constants.RECENTS.FILTER
        };

        return config;
    };

    this.getResourceForEndpoint = function (endpoint) {
        var resource = this.getConfig().resource;
        if (Utilities.isUndefinedOrNull(endpoint)) {
            return resource;
        }

        // Return resource from config data.
        for (var configEndpoint in constants.O365CONFIG.ENDPOINTS) {
            if (constants.O365CONFIG.ENDPOINTS.hasOwnProperty(configEndpoint) && endpoint.indexOf(configEndpoint) > -1) {
                resource = constants.O365CONFIG.ENDPOINTS[configEndpoint];
                break;
            }
        }

        return resource;
    };

    this.lookupUserInfo = async function (tokenData) {
        var userInfo = getUserProfile(tokenData.id_token);
        const oneDriveUrl = await getOneDriveUrl();
        userInfo.oneDriveUrl = oneDriveUrl;

        const sharePointUrl = await getSharePointUrl();
        userInfo.sharePointUrl = sharePointUrl || constants.LINKS.APP.SHAREPOINT_DEFAULT;
        return userInfo;
    };

    async function getOneDriveUrl() {
        var request = {
            method: 'GET',
            url: constants.O365CONFIG.ONEDRIVE_URL,
            headers: {
                'X-UserType': constants.USER_TYPE.O365
            }
        };

        var userService = $injector.get('userService');
        try {
            var data = await userService.httpRequest(request);
            if (data.webUrl) {
                return data.webUrl;
            }

            // There is no service info for OD4B
            return Promise.reject('o365UserService.DiscoverServiceEndpoint - Invalid resource');
        }
        catch (reason) {
            clearToken();
            notification.show(constants.NOTIFICATION.INVALIDSUBSCRIPTION);
            $log.error('0365User.getOneDriveUrl failed: %s', reason);
            return null;
        }
    }

    async function getSharePointUrl() {
        var request = {
            method: 'GET',
            url: constants.O365CONFIG.SHAREPOINT_URL,
            headers: {
                'X-UserType': constants.USER_SERVICE_TYPE.O365
            }
        };

        var userService = $injector.get('userService');
        try {
            var data = await userService.httpRequest(request);
            if (data.webUrl) {
                return data.webUrl;
            }
            return Promise.reject('No SharePoint info for user');
        }
        catch (reason) {
            $log.error('0365User.getSharePointUrl failed: %s', reason);
            return null;
        }
    }

    function clearToken() {
        var userService = $injector.get('userService');
        userService.clearToken();
    }

    // Method from ADAL to extract user profile from idtoken
    // Reference: https://github.com/AzureAD/azure-activedirectory-library-for-js/blob/master/lib/adal.js
    function getUserProfile(idToken) {
        var user = {};
        var parsedJson = extractIdToken(idToken);
        if (parsedJson && parsedJson.hasOwnProperty('aud')) {
            if (parsedJson.aud.toLowerCase() === constants.O365CONFIG.CLIENT_ID.toLowerCase()) {
                if (parsedJson.hasOwnProperty('upn')) {
                    user.email = parsedJson.upn;
                } else if (parsedJson.hasOwnProperty('email')) {
                    user.email = parsedJson.email;
                }

                if (parsedJson.hasOwnProperty('puid')) {
                    user.puid = parsedJson.puid;
                }

                if (parsedJson.hasOwnProperty('tid')) {
                    user.tid = parsedJson.tid;
                }
            } else {
                $log.error('IdToken has invalid aud field');
            }
        }

        return user;
    }

    // Method from ADAL to extract user profile from idtoken
    // Reference: https://github.com/AzureAD/azure-activedirectory-library-for-js/blob/master/lib/adal.js
    function extractIdToken(encodedIdToken) {
        // id token will be decoded to get the username
        var decodedToken = decodeJwt(encodedIdToken);
        if (!decodedToken) {
            return null;
        }

        try {
            var base64IdToken = decodedToken.JWSPayload;
            var base64Decoded = base64DecodeStringUrlSafe(base64IdToken);
            if (!base64Decoded) {
                this._logstatus('The returned id_token could not be base64 url safe decoded.');
                return null;
            }

            // ECMA script has JSON built-in support
            return JSON.parse(base64Decoded);
        } catch (err) {
            $log.error('The returned id_token could not be decoded: ' + err.stack);
        }

        return null;
    }

    // Method from ADAL to extract user profile from idtoken
    // Reference: https://github.com/AzureAD/azure-activedirectory-library-for-js/blob/master/lib/adal.js
    function base64DecodeStringUrlSafe(base64IdToken) {
        // html5 should support atob function for decoding
        base64IdToken = base64IdToken.replace(/-/g, '+').replace(/_/g, '/');
        if (window.atob) {
            return decodeURIComponent(escape(window.atob(base64IdToken)));
        }
        return null;
    }

    // Method from ADAL to extract user profile from idtoken
    // Reference: https://github.com/AzureAD/azure-activedirectory-library-for-js/blob/master/lib/adal.js
    function decodeJwt(jwtToken) {
        var idTokenPartsRegex = /^([^.\s]*)\.([^.\s]+)\.([^.\s]*)$/;

        var matches = idTokenPartsRegex.exec(jwtToken);
        if (!matches || matches.length < 4) {
            $log.error('The returned id_token is not parseable.');
            return null;
        }

        var crackedToken = {
            header: matches[1],
            JWSPayload: matches[2],
            JWSSig: matches[3],
        };

        return crackedToken;
    }
}
