import Utilities from '../../../common/utilities';
import '../../experimentConstants';

'use strict';

export default function SharingAController($log, sharingService, userService, localize) {
    var self = this;

    // Exposed properties
    self.shareDoc = null;
    self.isChrome = Utilities.isChrome();
    self.localize = localize;

    // Exposed methods
    self.init = init;
    self.shareDocument = shareDocument;

    // Function declarations

    function init(doc) {
        self.shareDoc = doc;
        userService.getUserType().then(function (userType) {
            sharingService.setShareUrl(self.shareDoc, userType);
            sharingService.setFlagFromPermissions(self.shareDoc, userType);
        });
    }

    function shareDocument(clickEvent) {
        $log.trackEvent('ShareRecentDocument', {'application': self.shareDoc.application});
        var openActive = Utilities.isUndefinedOrNull(clickEvent) || Utilities.isUndefinedOrNull(clickEvent.ctrlKey) ? true : !clickEvent.ctrlKey;
        Utilities.navigateToUrlWithNewTab(self.shareDoc.shareUrl, openActive);
        window.close();
    }
}
