import angular from 'angular';
import 'angular-aria';
import 'angular-animate';
import './common';
import './user';
import './mru';
import './diagnostics';
import './popup';
import './experimentation';
import 'office-ui-fabric-core/dist/css/fabric.min.css';
import 'office-ui-fabric-js/dist/css/fabric.components.min.css';
import './css/styles.css';

export default angular
    .module('app', ['localization', 'ngAria', 'app.common', 'app.diagnostics', 'app.experimentation', 'app.user', 'app.mru', 'app.popup'])
    .config(['$ariaProvider', '$compileProvider', '$httpProvider', '$provide', function ($ariaProvider, $compileProvider, $routeProvider, $httpProvider, $provide) {

        'use strict';

        // Configure Angular accessibility; opt out of tabindex handling
        $ariaProvider.config({
            tabindex: false
        });

        // Override the default regular expression that is used for whitelisting of safe urls during img[src] sanitization
        // eslint-disable-next-line @microsoft/sdl/no-angularjs-sanitization-whitelist
        $compileProvider.imgSrcSanitizationWhitelist(/^\s*(https?|data|chrome-extension|ms-browser-extension):/);
    }])
    .name;
