import fabric from 'office-ui-fabric-js/dist/js/fabric.min.js';
import BrowserHandler from '../common/browserHandler';
import constants from '../common/constants';
import Utilities from '../common/utilities';

// Custom directive since Angular does not support on change event binding with input elements of type 'file'
export function bindFileChange() {
    return {
        require: 'ngModel',
        restrict: 'A',
        link: function (scope, element, attrs, ngModel) {
            element.bind('change', function (event) {
                ngModel.$setViewValue(event.target.files);
            });
        },
    };
}

export function bindDragAndDrop($log) {
    return {
        require: 'ngModel',
        restrict: 'A',
        link: function (scope, element, attrs, ngModel) {
            element.bind('dragover', function (event) {
                event.stopPropagation();
                event.preventDefault();
                event.originalEvent.dataTransfer.dropEffect = 'copy';
            });

            element.bind('drop', function (event) {
                event.stopPropagation();
                event.preventDefault();
                $log.trackEvent('OpenDocument_Local_AppDragAndDrop');
                ngModel.$setViewValue(event.originalEvent.dataTransfer.files);
            });
        },
    };
}

export function bindFileInputContainerClick($log) {
    return {
        restrict: 'A',
        link: function (scope, element) {
            element.bind('mousedown keydown keypress', function (event) {
                // Bind with Mousedown and Enter keydown events
                if (
                    event.which === constants.JQUERY.EVENT_ID.MOUSEDOWN.LEFT_BUTTON_CLICK ||
                    event.which === constants.JQUERY.EVENT_ID.KEYPRESS.ENTER
                ) {
                    $log.trackEvent('OpenDocument_Local');
                    event.currentTarget.nextElementSibling.click();
                    event.preventDefault();
                }
            });
        },
    };
}

export function PopupController($timeout, $scope, $q, $log, $document, storage, userService, localize, notification) {
    var self = this;

    self.accountPanelOpened = false;
    self.filesSelected = null;
    self.webRedirectInputChecked = false;
    self.telemetryInputChecked = false;
    self.onLine = true;
    self.username = '';
    self.profilePicture = '';
    self.rtl = Utilities.isRTL();
    self.isChrome = Utilities.isChrome();
    self.useAuthNext = false;
    // Editor specific
    // Indicates that the manifest is turned on
    self.editorFeatureEnabled = Utilities.isEditorEnabled();

    // Use the blurb that mentions Editor only if this build
    // has editor enabled
    // var message = Utilities.getAppDescription(); // the manifest isn't localized so can't show this
    self.appDescriptionMessage = localize.getLocalizedString("AppDescription");

    // Exposed Methods
    self.onMenuItemClick = onMenuItemClick;
    self.onSignOutClick = onSignOutClick;
    self.onWelcomeSignInClick = onWelcomeSignInClick;
    self.onProfileClick = onProfileClick;
    self.onSignOutLinkClick = onSignOutLinkClick;
    self.onSignupLinkClick = onSignupLinkClick;
    self.onWebRedirectInputChange = onWebRedirectInputChange;
    self.onTelemetryInputChange = onTelemetryInputChange;
    self.onFileInputChange = onFileInputChange;
    self.openDocument = openDocument;
    self.openOfficeHome = openOfficeHome;
    self.openSupportPage = openSupportPage;
    self.openMyAccountPage = openMyAccountPage;
    self.browseToRecents = browseToRecents;
    self.createDocument = createDocument;
    self.handleTableNavigation = handleTableNavigation;
    self.loadFile = loadFile;
    self.constants = constants;
    self.storage = storage;
    self.userService = userService;
    self.localize = localize;
    self.hasSignedIn = constants.SIGNINSTATUS.UNKNOWN;

    // Default to using OneDrive as storage
    self.currentViewMode = constants.MENU_VIEWMODE.NONE;
    $scope.userType = constants.USER_TYPE.NONE;

    self.appLaunchers = [
        [
            {
                id: 'Outlook',
                label: 'OutlookAppName',
                iconClass: 'ms-BrandIcon--icon48 ms-BrandIcon--outlook',
                url: constants.LINKS.APP.OUTLOOK_DEFAULT,
                isBusinessOnly: false,
            },
            {
                id: 'OneDrive',
                label: 'OneDriveAppName',
                iconClass: 'ms-BrandIcon--icon48 ms-BrandIcon--onedrive',
                url: constants.LINKS.APP.ONEDRIVE,
                isBusinessOnly: false,
            },
        ],
        [
            {
                id: 'WordOnline',
                label: 'WordAppName',
                iconClass: 'ms-BrandIcon--icon48 ms-BrandIcon--word',
                url: constants.LINKS.APP.WORD,
                isBusinessOnly: false,
            },
            {
                id: 'ExcelOnline',
                label: 'ExcelAppName',
                iconClass: 'ms-BrandIcon--icon48 ms-BrandIcon--excel',
                url: constants.LINKS.APP.EXCEL,
                isBusinessOnly: false,
            },
        ],
        [
            {
                id: 'PowerPointOnline',
                label: 'PowerPointAppName',
                iconClass: 'ms-BrandIcon--icon48 ms-BrandIcon--powerpoint',
                url: constants.LINKS.APP.POWERPOINT,
                isBusinessOnly: false,
            },
            {
                id: 'OneNoteOnline',
                label: 'OneNoteAppName',
                iconClass: 'ms-BrandIcon--icon48 ms-BrandIcon--onenote',
                url: constants.LINKS.APP.ONENOTE,
                isBusinessOnly: false,
            },
        ],
        [
            {
                id: 'SharePoint',
                label: 'SharePointSiteAppName',
                iconClass: 'ms-BrandIcon--icon48 ms-BrandIcon--sharepoint',
                url: constants.LINKS.APP.SHAREPOINT_DEFAULT,
                isBusinessOnly: true,
            },
            {
                id: 'Teams',
                label: 'TeamsAppName',
                iconClass: 'ms-BrandIcon--icon48 ms-BrandIcon--teams',
                url: constants.LINKS.APP.TEAMS,
                isBusinessOnly: true,
            },
        ],
    ];

    // Send telemetry asynchronously after DOM content is loaded
    $document.ready(function () {
        $timeout(function () {
            $log.trackEvent('PopupPageLoad');
        });

        var CheckBoxElements = document.querySelectorAll('.ms-CheckBox');
        for (var j = 0; j < CheckBoxElements.length; j++) {
            new fabric.CheckBox(CheckBoxElements[j]);
        }
    });

    // Initialize scope variables for view
    function initialize() {
        storage.get(['userType', 'identity']).then(storageResult => {
            var signInStatus = (storageResult && storageResult.identity && storageResult.userType !== constants.USER_TYPE.NONE);
            self.hasSignedIn = signInStatus ? constants.SIGNINSTATUS.SIGNEDIN : constants.SIGNINSTATUS.NONE;

            // note that we are only checking against SIGNINSTATUS.HASREFRESHTOKEN because getSignInInfo is not supposed to return SIGNINSTATUS.SIGNEDIN since it does not attempt to valid the signin state
            if (self.hasSignedIn > constants.SIGNINSTATUS.NONE) {
                refreshSignInStatus(); // refresh signIn status only if we have signInInfo
            }

            return self.storage.get('webRedirect_disabled');
        }).then(storageResult => {
            self.webRedirectInputChecked = !(Utilities.isNotUndefinedOrNull(storageResult.webRedirect_disabled) && storageResult.webRedirect_disabled);

            return $log.getEnabledSetting();
        }).then(enabled => {
            $log.setEnabledSetting(enabled);
            self.telemetryInputChecked = enabled;

            return userService.useAuthNext();
        }).then(useAuthNext => {
            self.useAuthNext = useAuthNext;

            return;
        });
    }

    // Gets the user's profile picture from the user service
    function getUserPhoto() {
        userService.getUserPhoto().then(function (photo) {
            if (photo) {
                self.profilePicture = photo;
            }
        }, function (error) {
            self.profilePicture = '';
            $log.info(`PopupController.getUserPhoto: Profile Picture Error - ${error}`);
        });
    }

    // Update which menu is displayed
    function onMenuItemClick(viewMode) {
        // If the user clicks on the menu button again, dismiss the menu
        self.currentViewMode = self.currentViewMode === viewMode ? constants.MENU_VIEWMODE.NONE : (self.currentViewMode = viewMode);
    }

    function refreshSignInStatus() {
        $log.debug('PopupController.refreshSignInStatus(): Method start');

        self.userService.getUserType().then(async function (userType) {
            $log.debug(`PopupController.getSignInStatus(): hasSignedIn: ${userType}`);
            $log.trackEvent('PopupSignedIn', {'userType': userType});
            $scope.userType = userType;
            self.hasSignedIn = userType === constants.USER_TYPE.NONE ? constants.SIGNINSTATUS.NONE : constants.SIGNINSTATUS.SIGNEDIN;

            if (self.hasSignedIn !== constants.SIGNINSTATUS.SIGNEDIN) {
                return;
            }

            var userInfo = await self.userService.waitForUserInfo(userType);
            if (Utilities.isUndefinedOrNull(userInfo)) {
                $log.warn('self.UserService.getUserInfo(): userInfo is undefined or null when hasSignedIn is SIGNINSTATUS.SIGNEDIN');
                return;
            }

            if (Utilities.isUndefinedOrNull(userInfo.email)) {
                $log.warn('self.UserService.getUserInfo(): userInfo.email is undefined or null when hasSignedIn is SIGNINSTATUS.SIGNEDIN');
                return;
            }
            self.username = userInfo.email;

            // Get the user's profile picture
            getUserPhoto();
        });
    }

    //
    // --- Start Account Menu Methods ---
    //

    // Account menu item Sign-in/Sign-out link - Go back to login page
    // This should only be reachable when signed in though
    function onSignOutClick() {
        self.currentViewMode = constants.MENU_VIEWMODE.NONE;

        if (self.hasSignedIn === constants.SIGNINSTATUS.SIGNEDIN) {
            onSignOutLinkClick();
        }
    }

    // Welcome page Sign-in link
    async function onWelcomeSignInClick(userType) {
        $log.trackEvent('PopupSignInLinkClick');
        window.close();
        // eslint-disable-next-line no-async-promise-executor
        return new Promise(async (resolve) => {

            BrowserHandler.runtime.sendMessage({
                activity: constants.ACTIVITY.AUTHENTICATION.NAME,
                type: userType
            }, result => {
                resolve();
                $log.debug(`onWelcomeSignInClick sendMessage returned ${result}`);
            });
        });
    }

    // Sign-out link
    function onSignOutLinkClick() {
        $log.trackEvent('PopupSignOutLinkClick');

        BrowserHandler.runtime.sendMessage({
            activity: constants.ACTIVITY.LOGOUT.NAME,
            type: $scope.userType
        });

        self.hasSignedIn = constants.SIGNINSTATUS.NONE;
        $scope.userType = constants.USER_TYPE.NONE;

        // Clear recents from local storage
        storage.remove('localRecentDocuments');
    }

    // Sign Up link
    function onSignupLinkClick() {
        $log.trackEvent('PopupSignupLinkClick');
        Utilities.navigateToUrlWithNewTab(constants.LINKS.SIGNUP, true);
    }

    function onTelemetryInputChange() {
        self.telemetryInputChecked = !self.telemetryInputChecked;
        $log.setEnabledSetting(self.telemetryInputChecked);
        $log.trackEvent('PopupTelemetryInputChecked', {enabled: self.telemetryInputChecked});
    }

    function onWebRedirectInputChange() {
        self.webRedirectInputChecked = !self.webRedirectInputChecked;
        self.storage.set({webRedirect_disabled: !self.webRedirectInputChecked});
        $log.trackEvent('PopupWebRedirectChecked', {enabled: self.webRedirectInputChecked});
    }

    function onProfileClick() {
        self.accountPanelOpened = !self.accountPanelOpened;
    }

    // Open document
    async function openDocument() {
        $log.trackEvent('PopupOpenDocument', {'userType': $scope.userType});
        var userInfo = await self.userService.getUserInfo($scope.userType);
        if (Utilities.isUndefinedOrNull(userInfo)) {
            $log.warn('PopupController.openDocument(): invalid userInfo');
            return;
        }

        if (Utilities.isUndefinedOrNull(userInfo.oneDriveUrl)) {
            $log.warn('PopupController.openDocument(): invalid OneDriveUrl');
            return;
        }

        Utilities.navigateToUrlWithNewTab(userInfo.oneDriveUrl, true);
    }

    // Open SharePoint site
    async function openSharePointSite() {
        var userInfo = await self.userService.getUserInfo($scope.userType);
        if (Utilities.isNotUndefinedOrNull(userInfo) && Utilities.isNotUndefinedOrNull(userInfo.sharePointUrl)) {
            Utilities.navigateToUrlWithNewTab(userInfo.sharePointUrl, true);
            return;
        }

        $log.warn('PopupController.openDocument(): invalid sharePointUrl');
        Utilities.navigateToUrlWithNewTab(constants.LINKS.APP.SHAREPOINT_DEFAULT, true);
    }

    // Open Office.com
    function openOfficeHome() {
        Utilities.navigateToUrlWithNewTab(constants.LINKS.OFFICE_HOME_URL, true);
    }

    // Open support page
    function openSupportPage() {
        // Editor specific
        var supportUrl = Utilities.shouldShowEditorMaterial() ? constants.LINKS.SUPPORT_URL_EDITOR : constants.LINKS.SUPPORT_URL;
        Utilities.navigateToUrlWithNewTab(supportUrl, true);
    }

    // Open myaccount page
    function openMyAccountPage() {
        var url = signedInWithOrgId() ? constants.LINKS.MYACCOUNT_O365_URL : constants.LINKS.MYACCOUNT_MSA_URL;
        Utilities.navigateToUrlWithNewTab(url, true);
    }

    // Open Recent Document view in browser tab
    async function browseToRecents() {
        $log.trackEvent('PopupBrowseToRecents', {'userType': $scope.userType});

        // Can browse directly to mru for OneDrive Consumer with query string param
        var userInfo = await self.userService.getUserInfo($scope.userType);
        if (Utilities.isUndefinedOrNull(userInfo)) {
            $log.warn('PopupController.browseToRecents(): invalid userInfo');
            return;
        }

        if (Utilities.isUndefinedOrNull(userInfo.oneDriveUrl)) {
            $log.warn('PopupController.browseToRecents(): invalid OneDriveUrl');
            return;
        }

        var url = new URL(userInfo.oneDriveUrl);
        if (!signedInWithOrgId()) {
            var params = new URLSearchParams(url.search);
            params.append('qt', 'mru');
            url.search = params.toString();
        }
        Utilities.navigateToUrlWithNewTab(url.toString(), true);
    }

    // Create document
    function createDocument(app, clickEvent) {
        $log.trackEvent('PopupCreateDocument', {'application': app.id});

        var url;
        if (app.id === 'OneDrive') {
            openDocument();
            return;
        } else if (app.id === 'Outlook') {
            url = signedInWithOrgId() ? constants.LINKS.APP.OUTLOOK_O365 : constants.LINKS.APP.OUTLOOK_DEFAULT;
        } else if (app.id === 'SharePoint') {
            openSharePointSite();
            return;
        } else if (typeof app.url === 'function') {
            // Auth code for WAC: 0-default(hrd), 1-MSA, 2-OrgId
            const authType = (self.hasSignedIn !== constants.SIGNINSTATUS.SIGNEDIN) ? '0' : (signedInWithOrgId() ? '2' : '1');
            url = app.url(authType);
        } else {
            url = app.url;
        }

        Utilities.navigateToUrlWithNewTab(
            url,
            Utilities.isUndefinedOrNull(clickEvent) || Utilities.isUndefinedOrNull(clickEvent.ctrlKey) ? true : !clickEvent.ctrlKey
        );
    }

    // File picker
    function onFileInputChange() {
        self.loadFile();
        $log.trackEvent('PopupFileInputChange');
    }

    // Load the input file from html5, upload and view.
    function loadFile() {
        $log.trackEvent('PopupLoadFile', {filesSelectedCount: self.filesSelected.length});

        // User can multi-select multiple files and drag/drop
        if (self.filesSelected.length > 1) {
            $log.warn(`loadFile called with ${self.filesSelected.length} files selected`);
        }

        // Check whether user has chosen an unsupported file type
        if (!constants.FILE.SUPPORTED_FILE_TYPES[Utilities.getFileExtension(self.filesSelected[0].name)]) {
            $log.info(
                `PopupController.loadFile(): User attempted to open an unsupported file type of ${Utilities.getFileExtension(
                    self.filesSelected[0].name
                )} extension`
            );
            notification.show(constants.NOTIFICATION.UNSUPPORTEDFILETYPE);
            return;
        }

        sendFileUploadMessageToBackground(self.filesSelected[0]);
    }

    // Check if the user is currently signed in with orgId.
    function signedInWithOrgId() {
        return $scope.userType === constants.USER_TYPE.O365;
    }

    // Sends post message to background for processing
    function sendFileUploadMessageToBackground(file) {
        BrowserHandler.extension.getBackgroundPage().postMessage(file, window.location);
    }

    function handleTableNavigation(event) {
        var key = event.which || event.keyCode || 0;
        var supportedKey = [
            constants.JQUERY.EVENT_ID.KEYPRESS.ARROWLEFT,
            constants.JQUERY.EVENT_ID.KEYPRESS.ARROWUP,
            constants.JQUERY.EVENT_ID.KEYPRESS.ARROWRIGHT,
            constants.JQUERY.EVENT_ID.KEYPRESS.ARROWDOWN
        ];
        if (!supportedKey.includes(key)) {
            return;
        }
        var currentCell = (event.target).closest('.app_table_cell');
        var currentRow = currentCell.closest('.app_table_row');
        if (!currentCell || !currentRow) {
            return;
        }
        var prevCell = currentCell.previousElementSibling;
        var targetCell;
        switch (key) {
            case constants.JQUERY.EVENT_ID.KEYPRESS.ARROWLEFT:
                {
                    targetCell = currentCell.previousElementSibling;
                    break;
                }
            case constants.JQUERY.EVENT_ID.KEYPRESS.ARROWUP:
                {
                    var prevRow = currentRow.previousElementSibling;
                    if (prevRow) {
                        targetCell = prevRow.querySelector('.app_table_cell');
                    }
                    if (prevCell && targetCell) {
                        targetCell = targetCell.nextElementSibling;
                    }
                    break;
                }
            case constants.JQUERY.EVENT_ID.KEYPRESS.ARROWRIGHT:
                {
                    targetCell = currentCell.nextElementSibling;
                    break;
                }
            case constants.JQUERY.EVENT_ID.KEYPRESS.ARROWDOWN:
                {
                    var nextRow = currentRow.nextElementSibling;
                    if (nextRow) {
                        targetCell = nextRow.querySelector('.app_table_cell');
                    }
                    if (prevCell && targetCell) {
                        targetCell = targetCell.nextElementSibling;
                    }
                    break;
                }
        }
        if (targetCell) {
            event.preventDefault();
            targetCell.querySelector('button').focus();
        }
    }

    // Initialize
    initialize();
}
