import angular from 'angular';
import ariaTelemetry from './ariaTelemetry.service';
import telemetry from './telemetry.service';

export default angular
    .module('app.diagnostics', [])
    .service('telemetry', ['$injector', 'ariaTelemetry', telemetry])
    .service('ariaTelemetry', ['$window', ariaTelemetry])
    .name;

