import Utilities from '../common/utilities';
import './mru.service';
import constants from '../common/constants';

export default mruController;

function mruController($scope, $q, $log, storage, mruService) {
    var self = this;

    // Exposed methods
    self.onLoad = onLoad;
    self.openRecentDocument = openRecentDocument;

    // Exposed properties
    self.constants = constants;
    self.storage = storage;
    self.currentDisplayPanel = self.constants.RECENTS.DISPLAY_PANEL.LOADING;
    self.recentDocuments = [];
    self.LIST_LENGTH = self.constants.RECENTS.LIST_LENGTH_MSA;

    function onLoad() {
        $log.debug('Loading MruController');
        // Set to the loading message while we fetch the documents
        self.currentDisplayPanel = self.constants.RECENTS.DISPLAY_PANEL.LOADING;

        // Pull cached list of recent documents UX info
        self.getRecentDocumentsFromStorage().then(function (docList) {
            if (!Utilities.isUndefinedOrNull(docList) && docList.length > 0) {
                // We got some recents, so show them
                self.recentDocuments = docList;
                self.currentDisplayPanel = self.constants.RECENTS.DISPLAY_PANEL.LIST;
            }
        });
    }

    $scope.$watch('userType', function (userType) {
        if (userType === self.constants.USER_TYPE.NONE) {
            return;
        }

        self.LIST_LENGTH = userType === self.constants.USER_TYPE.O365 ? self.constants.RECENTS.LIST_LENGTH_O365 : self.constants.RECENTS.LIST_LENGTH_MSA;

        // Refresh the list
        self.refreshRecentDocumentsList().then(function () {
            // If there are some documents in the list already (from storage), then display them
            // Otherwise, we didn't find any and just display the no documents message
            self.currentDisplayPanel = (!Utilities.isUndefinedOrNull(self.recentDocuments) && self.recentDocuments.length > 0) ?
                self.constants.RECENTS.DISPLAY_PANEL.LIST : self.constants.RECENTS.DISPLAY_PANEL.NO_DOCS;
        },
            function (error) {
                $log.debug(`MruController $scope.$watch error on refreshRecentDocumentsList: ${error}`);

                // If we receive an error AND we don't have any cached documents, we'll show an error message
                // This is better than giving the impression the user documents are now gone
                self.currentDisplayPanel = (!Utilities.isUndefinedOrNull(self.recentDocuments) && self.recentDocuments.length > 0) ?
                    self.constants.RECENTS.DISPLAY_PANEL.LIST : self.constants.RECENTS.DISPLAY_PANEL.ERROR;
            });
    });

    // Open recent document
    function openRecentDocument(doc, clickEvent) {
        $log.trackEvent('PopupOpenRecentDocument', {'application': doc.application});
        Utilities.navigateToUrlWithNewTab(doc.url, Utilities.isUndefinedOrNull(clickEvent) || Utilities.isUndefinedOrNull(clickEvent.ctrlKey) ? true : !clickEvent.ctrlKey);
        window.close();
    }

    // Gets the most recent document list if signed in and then updates the current list
    this.refreshRecentDocumentsList = function () {
        var deferred = $q.defer();

        $log.debug('MruController.refreshRecentDocumentsList(): Method start');

        mruService.getRecentDocumentList().then(function (recentDocumentsList) {
            if (Utilities.isUndefinedOrNull(recentDocumentsList)) {
                // getRecentDocumentsList returned a null array, so set it to empty
                // to clear out the displayed list for the user
                recentDocumentsList = [];
            }

            // This will be used to store the list returned from the service
            var newRecentDocuments = [];

            // Keep a separate count from i so we fill the list even if recents has folders
            var listCount = 0;

            // (re)populate the recentDocuments list
            for (var i = 0; i < recentDocumentsList.length && listCount < self.LIST_LENGTH; i++) {

                // Skip and warn about any undefined document applications
                if (Utilities.isUndefinedOrNull(recentDocumentsList[i].Application)) {
                    $log.warn(`Undefined document application with document type ${Utilities.getFileExtension(recentDocumentsList[i].FileName)}`);
                    continue;
                }

                // Ensure the file data from o365 is valid
                if (self.isFileDataValid(recentDocumentsList[i].FileName, recentDocumentsList[i].Application, recentDocumentsList[i].DocumentUrl, recentDocumentsList[i].Timestamp)) {
                    newRecentDocuments.push({
                        appLabel: self.constants.FILE.APPLICATION_LABEL[recentDocumentsList[i].Application.toLowerCase()],
                        application: recentDocumentsList[i].Application,
                        lastAccessed: new Date(recentDocumentsList[i].Timestamp).toLocaleDateString(),
                        imageSource: '../' + self.constants.FILE.APPLICATION_IMAGE_PATH[recentDocumentsList[i].Application.toLowerCase()],
                        name: self.shortenFilename(recentDocumentsList[i].FileName),
                        url: self.createOpenInWebUrl(recentDocumentsList[i].DocumentUrl),
                        id: recentDocumentsList[i].Id
                    });

                    listCount++;
                }
            }

            // Only update the list if we found something different
            if (!self.mruListsEqual(self.recentDocuments, newRecentDocuments)) {
                self.recentDocuments = newRecentDocuments;
                self.setRecentDocumentsToStorage(newRecentDocuments);
            }

            deferred.resolve();
        }, function (error) {
            // rejected promise from mruService.getRecentDocumentList
            deferred.reject(error);
        });

        return deferred.promise;
    };

    // Do a check if we have any updated files
    this.mruListsEqual = function (oldList, newList) {

        if (Utilities.isUndefinedOrNull(oldList) || Utilities.isUndefinedOrNull(newList) || oldList.length !== newList.length) {
            return false;
        }

        for (var i = 0; i < newList.length; i++) {
            if (newList[i].name !== oldList[i].name ||
                newList[i].url !== oldList[i].url ||
                newList[i].lastAccessed !== oldList[i].lastAccessed) {
                return false;
            }
        }

        return true;
    };

    // Gets the list of documents for the UX from local storage
    this.getRecentDocumentsFromStorage = function () {
        var deferred = $q.defer();

        storage.get('localRecentDocuments').then(function (result) {
            deferred.resolve(result.localRecentDocuments);
        });

        return deferred.promise;
    };

    // Puts the documents list for the UX into local storage
    this.setRecentDocumentsToStorage = function (docList) {
        storage.set({
            'localRecentDocuments': docList
        });
    };

    // Helper function to validate the MRU file data
    this.isFileDataValid = function (name, application, url, lastAccessed) {

        // Make sure it's a valid application with a label since we use this to get the label and image path
        if (Utilities.isUndefinedOrNull(application) || !self.constants.FILE.APPLICATION_LABEL[application.toLowerCase()]) {
            return false;
        }

        // Now just make sure the rest of the values are not empty
        return (Utilities.isNotUndefinedOrNull(name) && Utilities.isNotUndefinedOrNull(url) && Utilities.isNotUndefinedOrNull(lastAccessed));
    };

    // Appends on ?web=1 so the o365 documents open in WAC and are not downloaded
    this.createOpenInWebUrl = function (path) {
        if (self.constants.FILE.SUPPORTED_FILE_TYPES[Utilities.getFileExtension(path)]) {
            return (path + '?web=1');
        }

        // Return the path if it doesn't and in a known extension. For example, OneNote notebooks
        return path;
    };

    // Removes file extension (e.g. .docx) if they exist
    this.shortenFilename = function (fileName) {
        var name = fileName.replace(/\.[^/.]+$/, '');
        return name;
    };
}
