import BrowserHandler from './browserHandler';

window.dumpStorage = () => {
    if (!window.chrome) {
        return;
    }

    window.chrome.storage.local.get(null, result => {
        console.log(`result '${JSON.stringify(result, '\t')}'`);
    });
};

export default function ($q, $log) {
    return {
        get: function (keys) {
            var deferred = $q.defer();

            BrowserHandler.storage.local.get(keys, function (result) {
                $log.debug(`storage: get '${JSON.stringify(keys)}', result '${JSON.stringify(result)}'`);
                deferred.resolve(result);
            });

            return deferred.promise;
        },

        set: function (items) {
            $log.debug(`storage: set items '${JSON.stringify(items)}'`);
            BrowserHandler.storage.local.set(items);
        },

        remove: function (keys) {
            var deferred = $q.defer();

            BrowserHandler.storage.local.remove(keys, function (result) {
                $log.debug(`storage: remove '${JSON.stringify(keys)}', result '${JSON.stringify(result)}'`);
                deferred.resolve(result);
            });

            return deferred.promise;
        },

        clear: function () {
            var deferred = $q.defer();

            BrowserHandler.storage.local.clear(function (result) {
                $log.debug(`storage: clear result '${JSON.stringify(result)}'`);
                deferred.resolve(result);
            });

            return deferred.promise;
        },
    };
}
