import angular from 'angular';
import 'angular-route';
import '../common/browserHandler';
import {bindDragAndDrop, bindFileChange, bindFileInputContainerClick, PopupController} from './popup.controller';
import '../common';

export default angular
    .module('app.popup', ['ngRoute'])
    .controller('PopupController', ['$timeout', '$scope', '$q', '$log', '$document', 'storage', 'userService', 'localize', 'notification', PopupController])
    .directive('bindFileChange', bindFileChange)
    .directive('bindDragAndDrop', ['$log', bindDragAndDrop])
    .directive('bindFileInputContainerClick', ['$log', bindFileInputContainerClick])
    .config(['$routeProvider', ($routeProvider) => {
        $routeProvider
            .when('/', {
                templateUrl: './popup/popup.html',
                controller: 'PopupController',
                controllerAs: 'ctrl'
            })
            .otherwise({
                redirectTo: '/'
            });
    }])
    .name;
