import BrowserHandler from './browserHandler';
import Utilities from '../common/utilities';
import {mapStackTrace} from 'sourcemapped-stacktrace';

export default function logDecorator($delegate, telemetry) {
    // App Insights custom property values have a max length of 8192
    // Limit to 8000 to stay under the App Insights limit
    function getTruncatedString(string) {
        return string.length > 8000 ? string.substring(0, 8000) : string;
    }

    // Wrapper functions for telemetry in order to handle any errors from the telemetry service //

    // Gets the enabled setting from storage
    function getEnabledSetting() {
        return telemetry.getEnabledSetting();
    }

    // Sets the enabled setting
    function setEnabledSetting(enabled) {
        try {
            telemetry.setEnabledSetting(enabled);
        } catch (error) {
            $delegate.error(`setEnabledSetting error: ${error}`);
        }
    }

    // Tracks a custom event
    function trackEvent(eventName, properties, measurements) {
        try {
            telemetry.trackEvent(eventName, properties, measurements);
        } catch (error) {
            $delegate.error(`trackEvent error with eventName ${eventName}: ${error}`);
        }
    }

    // Tracks a custom trace (logging/diagnostics info)
    function trackTrace(message, properties) {
        try {
            telemetry.trackTrace(message, properties);
        } catch (error) {
            // empty - if trackTrace fails, then error, info, warn, and debug statements can't be logged
        }
    }

    function trimExtensionOrigin(url) {
        try {
            const extensionOrigin = (new URL(location.href)).origin + '/';
            url = url.toString().replace(extensionOrigin, '');
        }
        // eslint-disable-next-line no-empty
        catch {}

        return url;
    }

    // Sends the log in a message where the background script will handle it and log
    // the message to the console. Only useful in cases like the popup where an event
    // may get logged before you can open the debugger window
    function logToBackground(message, severity, stack, filename, source) {
        if (Utilities.isBackgroundContext()) {
            try {
                const now = new Date();
                var timeStamp = now.toLocaleString(undefined, {month: 'numeric', day: 'numeric', hour: "numeric", minute: "numeric", second: "numeric"});
                var log = `[${timeStamp}] (${trimExtensionOrigin(source)}) ${filename ? filename + ' ' : ''} ${message}`;
                console[severity.toLowerCase()](log);
            }
            // eslint-disable-next-line no-empty
            catch {}
            return;
        }

        BrowserHandler.runtime.sendMessage({
            activity: 'log',
            severity: severity,
            log: message,
            filename: filename,
            stack: stack
        });
    }

    function messageString(message) {
        if (typeof message === 'string') {
            return message;
        }
        return JSON.stringify(message);
    }

    // Returns a new logging function (error, info, etc.) that extends the original logging function
    var prepareLog = function (originalFunction, severityLevel) {
        return function () {
            var args = [].slice.call(arguments);
            var logParams = args[0];
            var fullStack = undefined;

            if (Utilities.isExtensionInDevelopmentMode()) {
                fullStack = Error().stack;
            }

            var message = getTruncatedString(messageString(logParams.message ? logParams.message : logParams));

            new Promise((resolve) => {
                if (logParams.stack) {
                    resolve(typeof logParams.stack === 'string' ? getTruncatedString(logParams.stack).split('\n') : logParams.stack);
                } else if (fullStack) {
                    mapStackTrace(fullStack, mappedStack => {
                        // remove this file from the top of the stack
                        while (mappedStack[0].match('logDecorator.js')) {
                            mappedStack.shift();
                        }

                        resolve(mappedStack);
                    }, {/*cacheGlobally: true*/});
                } else {
                    resolve([]);
                }
            }).then((stack) => {
                var filename = '';
                var source = logParams.source || location;
                if (logParams.filename) {
                    filename = logParams.filename;
                } else {
                    for (var caller of stack) {
                        var matched = caller.match(/\b[\w.]+\b:\d+/g);
                        if (!matched) {
                            continue;
                        }
                        filename = matched.pop();
                        break;
                    }
                }

                logToBackground(message, severityLevel, stack, filename, source);
                var timeStamp = new Date().toLocaleString(undefined, {month: 'numeric', day: 'numeric', hour: "numeric", minute: "numeric", second: "numeric"});

                args[0] = `[${timeStamp}] ${filename ? filename + ' ' : ''}${severityLevel}: ${message}`;

                // Only output logs in development mode
                if (Utilities.isExtensionInDevelopmentMode()) {
                    originalFunction.apply(null, args);
                }

                // Do not track debug trace messages to telemetry
                if (severityLevel === 'DEBUG') {
                    return;
                }

                trackTrace(severityLevel, {
                    message: message,
                    stack: severityLevel === 'ERROR' ? stack : '',
                });
            });
        };
    };

    // Extend the original $delegate logging functions
    $delegate.debug = prepareLog($delegate.debug, 'DEBUG');
    $delegate.info = prepareLog($delegate.info, 'INFO');
    $delegate.warn = prepareLog($delegate.warn, 'WARN');
    $delegate.error = prepareLog($delegate.error, 'ERROR');

    // Attach telemetry functions to the $delegate so that $log can replace the telemetry service throughout the application
    $delegate.getEnabledSetting = getEnabledSetting;
    $delegate.setEnabledSetting = setEnabledSetting;
    $delegate.trackEvent = trackEvent;
    $delegate.trackTrace = trackTrace;

    return $delegate;
}
