import Utilities from '../../common/utilities';
import '../experimentConstants';
import constants from '../../common/constants';

'use strict';

export default function sharingService($http, $q, $log, userService) {
    var service = {
        setShareUrl: setShareUrl,
        setFlagFromPermissions: setFlagFromPermissions
    };

    return service;

    // Sets the shareUrl of the given document (currently only supported for MSA users)
    function setShareUrl(doc, userType) {
        if (Utilities.isUndefinedOrNull(doc) || Utilities.isUndefinedOrNull(doc.url)) {
            return;
        }

        doc.shareUrl = '';

        if (userType === constants.USER_TYPE.MSA) {
            var docUrl = doc.url;
            var shareUrl = docUrl.replace('&page=view', '&page=self');
            if (shareUrl.indexOf('&page=self') < 0) {
                shareUrl += '&page=self';
            }
            shareUrl += '&action=share';

            doc.shareUrl = shareUrl;
        }
    }

    // Sets the canBeShared flag on the given document based on whether the user has permission to share it (for MSA users)
    function setFlagFromPermissions(doc, userType) {

        // no document => do nothing
        if (Utilities.isUndefinedOrNull(doc)) {
            return $q.resolve();
        }

        // msa user => based on document permissions
        if (userType === constants.USER_TYPE.MSA) {
            var deferred = $q.defer();
            // in case of error in getting document permissions, assume the document can't be shared
            getMSAPermissions(doc).then(
                function (canShare) {
                    doc.canBeShared = canShare;
                    deferred.resolve();
                },
                function (error) {
                    $log.error(`sharingService.setFlagFromPermissions error: ${error}`);
                    doc.canBeShared = false;
                    deferred.resolve();
                });

            return deferred.promise;

        } else {
            // non-msa users can't share documents
            doc.canBeShared = false;
            return $q.resolve();
        }
    }

    // Resolves with true iff the MSA user has permission to share the given document
    function getMSAPermissions(doc) {
        var deferred = $q.defer();

        // get the cid (needed to find the userId)
        getCID().then(function (cid) {
            if (Utilities.isUndefinedOrNull(cid)) {
                deferred.reject('null cid');
                return;
            }

            // get the document id that OneDrive uses
            if (Utilities.isUndefinedOrNull(doc.id)) {
                deferred.reject('no document id');
                return;
            }
            var docSplit = doc.id.split('.');
            var docId = docSplit[docSplit.length - 1];

            // request to OneDrive api to get the permissions on the document
            var request = {
                method: 'GET',
                url: constants.MSACONFIG.ONEDRIVE_ITEMS_URL + docId + '/permissions',
                headers: {
                    'X-UserType': constants.USER_TYPE.MSA
                }
            };

            // make http request to get document permissions
            $http(request).then(
                function (response) {
                    var canShare = hasWriteAccess(response.data, cid);
                    deferred.resolve(canShare);
                },
                function (response) {
                    deferred.reject(response.statusText);
                });
        });

        return deferred.promise;
    }

    // Resolves with the user's cid or null if none exists
    function getCID() {
        var deferred = $q.defer();

        userService.waitForUserInfo(constants.USER_TYPE.MSA).then(function (userInfo) {
            if (Utilities.isUndefinedOrNull(userInfo)) {
                deferred.resolve(null);
                return;
            }
            deferred.resolve(userInfo.cid);
        });

        return deferred.promise;
    }

    // Returns true iff the user with the given userId has write access in the given permissions object
    function hasWriteAccess(permissions, userId) {
        if (Utilities.isUndefinedOrNull(permissions) || Utilities.isUndefinedOrNull(permissions.value)) {
            return true;
        }

        var canShare = true;

        // get the shortened id from the userId - this is the id that the OneDrive api uses for certain accounts
        var shortId = userId.substring(1, userId.length);

        for (var i = 0; i < permissions.value.length; ++i) {
            var permissionObject = permissions.value[i];
            if (Utilities.isUndefinedOrNull(permissionObject.grantedTo)) {
                continue;
            }
            var grantedToUser = permissionObject.grantedTo.user;
            if (grantedToUser.id === userId || grantedToUser.id === shortId) {
                var roles = permissionObject.roles;
                canShare = roles.toString().toLowerCase().indexOf('write') > -1;
            }
        }

        return canShare;
    }
}
